import moment from 'moment';

class FolderBrowserHelper {
    mapJsonToTableData = (entryData, projectList) => {
        return entryData.map((row) => {
            const applicationName = projectList[row.projectId] || 'Unknown Application';
            let dM = row.dateModified;
            let dateModifiedEdited= `${dM.substring(0, dM.length-2)}:${dM.substring(dM.length-2)}`;
            let dateModified;
            try {
                dateModified = new Date(dateModifiedEdited);
            } catch (e) {
                dateModified = row.dateModified;
            }
            // TODO: Update project/application naming convention
            return {
                name: row.name,
                key: row.id,
                type: row.type,
                subtype: row.subtype,
                projectId: row.projectId,
                applicationName: applicationName,
                dateModified,
                owner: row.owner.name,
                isCertified: row.certifiedInfo ? row.certifiedInfo.certified : false
            };
        });
    }
}

class SmartFolderBrowserHelper {
    mapJsonToTableData = (entryData) => {
        return entryData.map((row) => {
            const dateCreated = moment(row.dateCreated);
            const dateModified = moment(row.dateModified);
            return {
                ...row,
                dateCreated,
                dateModified,
            };
        });
    }
}

export const folderBrowserHelper = new FolderBrowserHelper();
export const smartFolderBrowserHelper = new SmartFolderBrowserHelper();
