/* eslint-disable no-param-reassign */
/* eslint-disable no-restricted-syntax */
/* eslint-disable class-methods-use-this */
/* eslint-disable consistent-return */
import { message } from 'antd';
import request from 'superagent';

import { applicationType } from './BackendHelper';
import { supportedTypes } from './Utilities';

const USE_PROXY = false;
const TOKEN_HEADER = 'X-MSTR-AuthToken';
const PROJECT_ID_HEADER = 'X-MSTR-ProjectID';
const MIN_ISERVER_VERSION = '11.1.0400';
message.config({ maxCount: 1 });


export const defineType = (subtype) => {
  const def = 'cube';
  const subtypeNum = Number(subtype);
  if (supportedTypes.reportTypes.includes(subtypeNum)) return 'report';
  if (supportedTypes.cubeTypes.includes(subtypeNum)) return 'cube';
  return def;
};
export default class MSTRFetch {
  constructor() {
    this.supportedTypes = [3, 8, 768, 769, 779, 776];
    // this.t = text => text;

    this.userInfo = {
      fullName: 'MSTR User',
      initials: 'MU',
    };
  }

  setCredentials(config) {
    const { url, username, password, loginMode } = config;
    this.credentials = { username, password, loginMode };
    this.envUrl = url;
    this.username = username;
    this.password = password;
    this.loginMode = loginMode;
  }

  get user() {
    return this.username;
  }

  get url() {
    return this.envUrl;
  }

  get isAuthenticated() {
    return this.token;
  }

  get projectId() {
    return this.project;
  }

  set projectId(id) {
    this.project = id;
  }

  set translateFunction(tF) {
    this.t = tF;
  }

  set tokenValue(tV) {
    this.token = tV;
  }

  logout() {
    this.token = null;
  }

    handleHTTPError = (error, mode = 'request') => {
      const { status } = error;
      let msg;
      if (status === 401 || status === 503) {
        let unauthMsg;
        if (mode === 'request') {
          unauthMsg = 'The user\'s session has expired, please reauthenticate.';
          this.logout();
        }
        else if (mode === 'auth') {
          unauthMsg = 'Your username, password or authentication mode is incorrect.';
        }
        msg = this.t(unauthMsg);
      }
      else if (status === 404 || !status) {
        msg = this.t('Could not process your request. The environment URL you provided may be incorrect or the server is down.');
      }
      else if (status === 500 && mode === 'auth') {
        msg = this.t('Your username, password or authentication mode is incorrect.');
      }
      else if (status === 403) {
        msg = this.t('Insufficient privileges to use this service. Contact your administrator.');
      }
      else if (status === 'INSUFFICIENT_ISERVER_VERSION') {
        msg = this.t('Your MicroStrategy version {{yourVersion}} is not supported by MSTRIO. Please upgrade to version {{minIServerVersion}} or higher.', { yourVersion: error.iServerVersion, minIServerVersion: MIN_ISERVER_VERSION });
      }
      else {
        msg = this.t('Could not process your request. Server responded with {{errorCode}} error code.', { errorCode: status });
      }
      if (msg !== undefined) message.warning(msg, 4);
      return Promise.reject(error);
    }

    checkIfOnline() {
      const url = `${this.envUrl}api/`;
      return request
        .get(url)
        .then(() => Promise.resolve())
        .catch((err) => {
          if (!err.status) {
            return this.handleHTTPError(err);
          }
          return Promise.resolve();
        });
    }

    getData(_endpoint, _projectId = '') {
      const projectId = this.project || _projectId;
      const endpoint = encodeURI(_endpoint);
      const url = `${this.envUrl}api/${endpoint}`;
      return request
        .get(url)
        .set('Accept', 'application/json')
        .set('Content-Type', 'application/json')
        .set(TOKEN_HEADER, this.token)
        .set(PROJECT_ID_HEADER, projectId)
        .withCredentials()
        .then((res) => res.body)
        .catch((err) => this.handleHTTPError(err));
    }

    postData(endpoint, data = {}, _projectId = '') {
      const projectId = this.project || _projectId;
      const url = `${this.envUrl}api/${endpoint}`;
      return request.post(url)
        .set('Accept', 'application/json')
        .set('Content-Type', 'application/json')
        .set(TOKEN_HEADER, this.token)
        .set(PROJECT_ID_HEADER, projectId)
        .send(JSON.stringify(data))
        .withCredentials()
        .then((res) => res.body)
        .catch((err) => this.handleHTTPError(err));
    }

    getToken = async () => {
      const url = `${this.envUrl}api/auth/login`;
      const data = {
        username: this.username,
        password: this.password,
        loginMode: this.loginMode,
        applicationType,
      };

      return request
        .post(url)
        .set('Accept', 'application/json')
        .set('Content-Type', 'application/json')
        .send(JSON.stringify(data))
        .withCredentials()
        .on('error', (err) => this.handleHTTPError(err, 'auth'))
        .then(async (response) => {
          const token = response.header['x-mstr-authtoken'];
          this.token = token;
          const hasPriveleges = await this.checkPrivileges();
          if (!hasPriveleges) {
            this.token = null;
          }
          return hasPriveleges;
        })
        .catch(() => false);
    }

    getIServerVersion = async () => this.getData('status')
      .then((res) => res.iServerVersion)
      .catch(() => this.handleHTTPError({ status: 'INSUFFICIENT_ISERVER_VERSION', iServerVersion: '' }))

    checkIServerVersion = async () => this.checkIfOnline()
      .then(() => this.getIServerVersion())
      .then((iServerVersion) => {
        if (iServerVersion < MIN_ISERVER_VERSION) {
          this.handleHTTPError({ status: 'INSUFFICIENT_ISERVER_VERSION', iServerVersion });
          return false;
        }
        return true;
      })
      .catch(() => false)

    getSessionInfo = async () => this.getData('sessions')
      .then((res) => {
        const { initials, fullName, timeout } = res;
        this.userInfo = {
          fullName,
          initials,
        };
        this.timeout = timeout;
      })

    getCubeInfo(cubeId) {
      return this.getData(`cubes/${cubeId}`);
    }

    getCube(cubeId, limit = -1) {
      return this.postData(`cubes/${cubeId}/instances?offset=0&limit=${limit}`);
    }

    getCubeAttributeElements(cubeId, instanceId, attributeId) {
      return this.getData(`cubes/${cubeId}/instance/${instanceId}/attributes/${attributeId}/elements`);
    }

    getReportInfo(reportId) {
      return this.getData(`reports/${reportId}`);
    }

    getReport(reportId) {
      return this.postData(`reports/${reportId}/instances?offset=0&limit=-1`);
    }

    getFolderInfo(folderId) {
      return this.getData(`objects/${folderId}?type=8`);
    }

    getReportAttributeElements(reportId, attributeId) {
      return this.getReportDetails(reportId, [attributeId], [], []).then((report) => {
        if (!report) {
          return;
        }
        const attributes = [];
        if (report.result && report.result.data.root && report.result.data.root.children) {
          report.result.data.root.children.forEach((item) => {
            attributes.push({ name: item.element.name, id: item.element.id });
          });
        }
        return attributes;
      });
    }

    getProjects = async () => this.getData('projects')
      .then((res) => res)
      .catch(() => [])

    getRootFolders(project = this.project) {
      return this.getData('folders?offset=0&limit=-1').then((folders) => {
        if (!folders) return;
        this.createUniqueFolderKey(folders, project);
        const res = this.sortAndFilterArray(folders.filter((folder) => !folder.hidden));
        return res;
      });
    }

    getMyPersonalObjects(project = this.project) {
      return this.getData('folders/myPersonalObjects').then((folders) => {
        this.createUniqueFolderKey(folders, project);
        return this.sortAndFilterArray(folders.filter((folder) => !folder.hidden));
      });
    }

    getFolderContent(folderId, project = this.project) {
      return this.getData(`folders/${folderId}?offset=0&limit=-1`)
        .then((folderContent) => {
          if (!folderContent) return;
          this.createUniqueFolderKey(folderContent, folderId + project);
          return this.sortAndFilterArray(folderContent);
        });
    }

    searchDataset(_name) {
      const name = _name.replace(/#/g, '');
      const encodedName = USE_PROXY ? name : encodeURIComponent(name);
      return this.getData(`searches/results?name=${encodedName}&pattern=4&type=3&getAncestors=true&limit=-1&certifiedStatus=ALL`)
        .then((response) => {
          let { result } = response;
          const { totalItems } = response;
          result = this.sortAndFilterArray(result);
          return { result, totalItems };
        });
    }

    // eslint-disable-next-line class-methods-use-this
    createUniqueFolderKey(array, prefix) {
      array.forEach((item) => {
        // eslint-disable-next-line no-param-reassign
        item.key = item.id + prefix;
      });
    }

    sortAndFilterArray(array) {
      if (!array || array.length === 0) {
        return [];
      }
      const filteredArray = array.filter((element) => this.supportedTypes.includes(element.type)
                || this.supportedTypes.includes(element.subtype)
                || !element.subtype);

      return filteredArray.sort((a, b) => {
        const nameA = a.name.toUpperCase();
        const nameB = b.name.toUpperCase();
        if (nameA < nameB) {
          return -1;
        }
        if (nameA > nameB) {
          return 1;
        }
        return 0;
      });
    }

    flattenResult(result, preview) {
      try {
        if (result && result.definition && result.data) {
          let attributes = [];
          let metrics = [];
          let row = [];
          const rawData = [];
          const attributesobj = [];
          let temp;
          const rowobj = {};
          result.definition.attributes.forEach((att) => {
            if (att.forms.length > 1) {
              att.forms.forEach((form) => {
                attributesobj.push(`${att.name} ${form.name}`);
              });
            } else {
              attributesobj.push(att.name);
            }
          });
          attributes = result.definition.attributes.map((att) => att.name);
          metrics = result.definition.metrics.map((mm) => mm.name);
          const attNum = attributes.length;
          const visit = (node) => {
            if (node.element) {
              rowobj[node.depth] = node.element.formValues;
            }

            if (node.metrics || !node.children) {
              metrics.forEach((metric, i) => {
                const value = preview ? node.metrics[metric].fv : node.metrics[metric].rv;
                rowobj[attNum + i] = value;
              });
              temp = {};
              row = {};
              attributes.forEach((att, i) => {
                temp[att] = rowobj[i];
              });
              metrics.forEach((metric, i) => {
                temp[metric] = rowobj[attNum + i];
              });

              // Resolve multi-form attributes
              for (const k in temp) {
                if (typeof (temp[k]) === 'object') {
                  if (Object.keys(temp[k]).length > 1) {
                    for (const form in temp[k]) {
                      if (form) {
                        row[`${k} ${form}`] = temp[k][form];
                      }
                    }
                  } else {
                    for (const form in temp[k]) {
                      if (form) {
                        row[k] = temp[k][form];
                      }
                    }
                  }
                } else {
                  row[k] = temp[k];
                }
              }
              rawData.push(row);
            }
          };

          const search = (root) => {
            if (root) {
              visit(root);
              root.visited = true;
              if (!root.metrics && root.children) {
                root.children.forEach((node) => {
                  if (!node.visited) {
                    search(node);
                  }
                });
              }
            }
          };

          search(result.data.root);
          return {
            attributes: attributesobj,
            metrics,
            data: rawData,
          };
        }
        return null;
      } catch (error) {
        return null;
      }
    }

    isSessionAlive = async () => this.getData('sessions')
      .then(() => true)
      .catch(() => false)

    sendDummyRequest = () => {
      this.getData('sessions');
    }

    checkPrivileges = async () => this.getData('projects')
      .then(() => true)
      .catch(() => false)

    // from folder-browser-rest.js
    getProjectsForBrowsing = async (envUrl, authToken) => request
      .get(`${envUrl}/projects`)
      .set('x-mstr-authtoken', authToken)
      .withCredentials()
      .then((res) => {
        const projectList = {};
        res.body && res.body.forEach((project) => {
          projectList[project.id] = project.name;
        });
        return projectList;
      })
      .catch((err) => this.handleHTTPError(err))

    getNumberOfElements = async (envUrl, authToken, typeId) => request
      .get(`${envUrl}/searches/results?pattern=4&type=${typeId}&getAncestors=false&limit=1&certifiedStatus=ALL`)
      .set('x-mstr-authtoken', authToken)
      .withCredentials()
      .then((res) => res.body.totalItems)
      .catch((err) => this.handleHTTPError(err))

    getLimitedSmartFolderContent = async (envUrl, authToken, typeId, offset, limit) => request
      .get(`${envUrl}/searches/results?pattern=4&type=${typeId}&getAncestors=false&limit=${limit}&offset=${offset}&certifiedStatus=ALL`)
      .set('x-mstr-authtoken', authToken)
      .withCredentials()
      .then((res) => res.body.result)
      .catch((err) => this.handleHTTPError(err))

    getObjectParams = async (envUrl, authToken, projectId, objectId) => request
      .get(`${envUrl}api/objects/${objectId}?type=3`)
      .set('x-mstr-authtoken', authToken)
      .set('x-mstr-projectid', projectId)
      .withCredentials()
      .then((res) => res.body)
      .catch((err) => this.handleHTTPError(err))

    getCubeSize = async (envUrl, authToken, projectId, objectId) => request
      .get(`${envUrl}api/cubes?id=${objectId}`)
      .set('x-mstr-authtoken', authToken)
      .set('x-mstr-projectid', projectId)
      .withCredentials()
      .then((res) => res.body.cubesInfos[0].size)
      .catch((err) => this.handleHTTPError(err))


    isPrompted = async (objectId, projectId) => {
      const url = `${this.envUrl}api/reports/${objectId}/prompts`;
      return request(url)
        .set('Accept', 'application/json')
        .set('Content-Type', 'application/json')
        .set(TOKEN_HEADER, this.token)
        .set(PROJECT_ID_HEADER, projectId)
        .withCredentials()
        .then((res) => {
          if (res.body.length > 0) return true;
          return false;
        })
        .catch((err) => this.handleHTTPError(err))
        .catch((err) => err.status);
    };

    createFolder = async (name, parent, projectId) => {
      const data = { name, parent };
      const url = `${this.envUrl}api/folders`;

      return request.post(url)
        .set('Accept', 'application/json')
        .set('Content-Type', 'application/json')
        .set(TOKEN_HEADER, this.token)
        .set(PROJECT_ID_HEADER, projectId)
        .send(JSON.stringify(data))
        .withCredentials()
        .then(() => ({ success: true }))
        .catch((err) => this.handleHTTPError(err, 'folder-creation'))
        .catch((err) => ({ success: false, message: err.response.body.message }));
    }
}
