/* eslint-disable consistent-return */
import React from 'react';
import moment from 'moment';
import { List } from 'react-virtualized';
import { Spin, Tree, Icon, Col } from 'antd';
import Tooltip from 'rc-tooltip';
import 'rc-tooltip/assets/bootstrap.css';
import setValueByPath from 'set-value';
import MSTRIcon from './MSTRIcon';
import StateContext from '../StateContext';
import { compareByName, disableFocusOnMouseClick } from '../utilities/Utilities';

import './folder-browser/folder-browser.css';

const { DirectoryTree } = Tree;
const { TreeNode } = Tree;
const loadingIndicator = <Icon type="loading" />;

/**
 * setPathIndex
 * Creates a string with the path to each node in the tree.
 * @return An array where each node has a path
 * @example
 * Object.parent[0].children[1] => 'parent.0.children.1'
 */
const setPathIndex = (array, parentPath) => array.map((node, index) => {
  if (node.type === 3 || node.emptyFolder) {
    node.isLeaf = true;
  } else {
    node.isLeaf = false;
  }
  return { ...node, path: parentPath + index.toString() };
});

class FolderTree extends React.Component {
  static createParentsString(ancestors) {
    let result = '';
    ancestors.forEach((ancestor) => {
      result += `${ancestor.name}/`;
    });
    return result;
  }

  static removeAllSelectedElements = () => {
    Array.from(document.querySelectorAll('.selected')).forEach((element) => element.classList.remove('selected'));
  }

  /**
   * Appends a `key` property to each array element, derived from its `id` property and the `prefix`.
   */
  static appendUniqueFolderKeys = (array, suffix) => array.map((item) => ({ ...item, key: item.id + suffix }))

  static setClassNameForSearchedElement = (emptyFolder, isSelected) => {
    if (emptyFolder) {
      return 'no-dataset';
    }
    let className = 'ant-tree-title list-element';
    className += isSelected ? ' selected' : '';
    return className;
  }

  constructor(props) {
    super(props);
    this.state = {
      mstrTree: null,
      searchName: '',
      searchTree: [],
      selectedElementForSearch: null,
      expandedKeys: [],
    };
  }

  componentDidMount = async () => {
    const { selectedProjects, api } = this.props;
    let projectTree = await api.getProjects();
    if (projectTree) {
      projectTree = selectedProjects ? projectTree.filter((e) => selectedProjects.includes(e.id)).sort(compareByName) : projectTree;
      const keyedTree = setPathIndex(FolderTree.appendUniqueFolderKeys(projectTree, 'PROJ'), '');
      this.setState({ mstrTree: keyedTree });
    } else {
      throw new Error('No project tree returned by getProjects!');
    }
    this.expandExpandedProject();
    document.addEventListener('keydown', this.handleKeyDown);
    disableFocusOnMouseClick('folder-tree-container');
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this.handleKeyDown, false);
  }

  getProjectId(path) {
    const index = Number(path.split('.')[0]);
    // eslint-disable-next-line react/destructuring-assignment
    return this.state.mstrTree[index].id;
  }

  handleKeyDown = (e) => {
    const folderTreeContainer = document.querySelector('.folder-tree-container');
    const treeNodes = [...folderTreeContainer.querySelectorAll('.ant-tree-node-content-wrapper')];
    treeNodes.forEach((element) => element.setAttribute('tabindex', -1));
    if (e.keyCode === 40 || e.keyCode === 38) {
      if (document.activeElement === folderTreeContainer) {
        e.preventDefault();
        const index = e.keyCode === 40 ? 0 : treeNodes.length - 1;
        treeNodes[index].focus();
      }
      else if (treeNodes.includes(document.activeElement)) {
        e.preventDefault();
        const index = treeNodes.findIndex((element) => element === document.activeElement);
        const incr = e.keyCode === 40 ? 1 : -1;
        const newIndex = index + incr;
        const finalIndex = newIndex > treeNodes.length - 1
          ? 0
          : newIndex < 0 ? treeNodes.length - 1 : newIndex;
        treeNodes[finalIndex].focus();
      }
    }

    if ((e.keyCode === 32 || e.keyCode === 13) && treeNodes.includes(document.activeElement)) {
      e.preventDefault();
      document.activeElement.click();
    }

    if (((e.keyCode === 37 || e.keyCode === 39) && treeNodes.includes(document.activeElement))) {
      const switcher = document.activeElement.parentElement.querySelector('.ant-tree-switcher');
      const isOpened = [...switcher.classList][1].endsWith('open');
      if ((isOpened && e.keyCode === 37) || (!isOpened && e.keyCode === 39)) {
        switcher.click();
      }
    }
  }

    expandExpandedProject = () => {
      const { expandedKeys } = this.props;
      const projectElementClassNames = expandedKeys.filter((e) => e.includes('PROJ')).map((e) => `C${e}`);
      projectElementClassNames.forEach((e) => {
        document.querySelector(`li.${e} span:first-child`).click();
      });
    }

    /**
   * Appears to only be used for rendering search list, its normal tree counterpart being {@link renderTreeNodes}
   */
    rowRenderer = ({ key, index, style }) => {
      const { searchTree, selectedElementForSearch } = this.state;
      const { t } = this.context;
      if (searchTree.length) {
        const element = searchTree[index];
        const isCube = element.subtype === 779 || element.subtype === 776;
        const icon = element.emptyFolder ? (
          <Icon type="info-circle-o" />
        ) : (
          <MSTRIcon type={isCube ? 'cube' : 'report'} />
        );
        const isSelected = selectedElementForSearch === element.id;
        return (
          <li>
            <span className="ant-tree-node-content-wrapper ant-tree-node-content-wrapper-normal">
              <Tooltip
                placement="topLeft"
                mouseLeaveDelay={0}
                mouseEnterDelay={0.5}
                trigger={['hover']}
                overlay={(
                  <div>
                    <div className="tooltip-header">
                      <div className="tooltip-first-line">{t('Location')}</div>
                      <div>{t('Last modified')}</div>
                    </div>
                    <div className="tooltip-description">
                      <div className="tooltip-first-line">{element.ancestors}</div>
                      <div>
                        {`${moment(element.dateModified).format('l')} ${moment(
                          element.dateModified,
                        ).format('LTS')}`}
                      </div>
                    </div>
                  </div>
                )}
              >
                <button
                  key={key}
                  value={key}
                  style={style}
                  onClick={element.emptyFolder ? undefined : this.selectListElement}
                  className={FolderTree.setClassNameForSearchedElement(
                    element.emptyFolder,
                    isSelected,
                  )}
                  type="button"
                >
                  {icon}
                  {element.name}
                </button>
              </Tooltip>
            </span>
          </li>
        );
      }
      return null;
    };


    selectListElement = (e) => {
      const elementPosition = e.currentTarget.getAttribute('value').split('-')[0];
      const { searchTree } = this.state;
      const { onSelect } = this.props;
      if (e.currentTarget.getAttribute('class').indexOf('selected') === -1) {
        FolderTree.removeAllSelectedElements();
        e.currentTarget.setAttribute('class', `${`${e.currentTarget.getAttribute('class')} selected`}`);
        const element = searchTree[elementPosition];
        onSelect([element.key], { selectedNodes: [element] });
        this.setState({
          selectedElementForSearch: element.id,
        });
      } else {
        this.setState({
          selectedElementForSearch: null,
        });
        FolderTree.removeAllSelectedElements();
      }
    }

    onSearch = async (name) => {
      const { searchName } = this.state;
      const { t, api } = this.props;
      if (!name) {
        this.setState({ searchName: '' });
      } else if (name === searchName) {
        return false;
      }
      this.setState({ searchName: name, searchTree: [] });
      const { projects } = api;
      const promises = projects.map(async (projectId) => {
        const response = await api.searchDataset(projectId, name);
        // If we are still searching for the name we just got results for
        if (searchName === name) {
          return this.updateSearchTree(response, projectId);
        }
      });
        // If nothing was found, all the array values will be undefined, making the array empty.
      const results = await Promise.all(promises);
      const isEmpty = !results.length;
      if (isEmpty) {
        this.setState({
          searchTree:
                    [{
                      name: t('No Data'),
                      key: Date.now(),
                      id: Date.now(),
                      emptyFolder: true,
                      isLeaf: true,
                      projectId: 'noresultsid',
                    }],
        });
      }
    }

    updateSearchTree = (response, projectId) => {
      const { searchTree } = this.state;
      const { result, totalItems } = response;
      if (!response || totalItems === 0) {
        return;
      }
      const results = result.map((res) => {
        const { id, name, type, subtype, ancestors, dateModified } = res;
        return {
          id,
          name,
          type,
          subtype,
          dateModified,
          ancestors: ancestors ? FolderTree.createParentsString(ancestors) : undefined,
          key: projectId + id,
          isLeaf: true,
          projectId,
        };
      });
      this.setState({ searchTree: [...searchTree, ...results] });
      return true;
    }

    onSearchChange = (e) => {
      if (!e.target.value) {
        this.setState({ searchName: '', selectedElementForSearch: null });
      }
    }

    onLoadData = (treeNode) => {
      const mstrObject = treeNode.props.dataRef;
      if (mstrObject.children) {
        return Promise.resolve();
      }

      // Projects don't have subtype
      if (!mstrObject.subtype) {
        return this.loadRootFolders(mstrObject);
      }

      if (mstrObject.personalObjects) {
        return this.loadMyPersonalObjects(mstrObject);
      }

      // Folders
      if (mstrObject.type === 8) {
        return this.loadFolderContent(mstrObject);
      }
    }

    loadRootFolders = async (mstrObject) => {
      const projectId = mstrObject.id;
      const { api } = this.context;
      api.projectId = projectId;
      const { path } = mstrObject;
      const personalObjectsNode = {
        id: projectId,
        key: `PO${projectId}`,
        personalObjects: true,
        name: 'My Personal Objects',
        type: 8,
        subtype: 2048,
      };
      const treeNodes = await api.getRootFolders(projectId);
      if (!treeNodes) return null;
      const keyedNodes = FolderTree.appendUniqueFolderKeys(treeNodes, projectId);
      keyedNodes.push(personalObjectsNode);
      return this.createTreeNodes(keyedNodes, path);
    }

    loadMyPersonalObjects = async (mstrObject) => {
      const { api } = this.context;
      const { path } = mstrObject;
      const projectId = this.getProjectId(mstrObject.path);
      const personalObjects = await api.getMyPersonalObjects(projectId);
      let treeNodes = FolderTree.appendUniqueFolderKeys(personalObjects, projectId);
      if (!treeNodes || treeNodes.length === 0) {
        treeNodes = [{
          name: 'No datasets',
          key: String(Date.now()),
          id: String(Date.now()),
          emptyFolder: true,
          isLeaf: true,
        }];
      }
      return this.createTreeNodes(treeNodes, path);
    }

    loadFolderContent = async (mstrObject) => {
      const { api } = this.context;
      const folderId = mstrObject.id;
      const { path } = mstrObject;
      const projectId = this.getProjectId(mstrObject.path);
      api.projectId = projectId;
      const { hideDatasets } = this.props;
      const { t } = this.context;
      let treeNodes = await api.getFolderContent(folderId, projectId);
      if (hideDatasets) treeNodes = treeNodes.filter((e) => e.type === 8);
      let keyedNodes = FolderTree.appendUniqueFolderKeys(treeNodes, folderId + projectId);
      const emptyFolderInfo = hideDatasets ? t('No subfolders') : t('No Data');
      if (!treeNodes || treeNodes.length === 0) {
        keyedNodes = [{
          name: emptyFolderInfo,
          id: String(Date.now()),
          key: String(Date.now()),
          emptyFolder: true,
          isLeaf: true,
        }];
      }
      return this.createTreeNodes(keyedNodes, path);
    }

    createTreeNodes(_treeNodes, path) {
      const { mstrTree } = this.state;
      const newMstrTree = mstrTree;
      const updatePath = `${path}.children`;

      const treeNodes = setPathIndex(_treeNodes, `${updatePath}.`);
      setValueByPath(newMstrTree, updatePath, treeNodes);

      this.setState({ mstrTree: newMstrTree });
    }


    renderTreeNodes(treeNodes) {
      if (!treeNodes) {
        return;
      }

      return treeNodes.map((node) => {
        const { t, api } = this.props;
        const key = api.token + node.key;
        const icon = (({ expanded, isLeaf }) => {
          if (node.emptyFolder) {
            return (<Icon type="info-circle-o" />);
          } if (!node.subtype) {
            return (<MSTRIcon type={expanded ? 'project-expanded' : 'project-collapsed'} />);
          } if (node.type === 8) {
            return (<MSTRIcon type={expanded ? 'folder-expanded' : 'folder-collapsed'} />);
          } if (isLeaf) {
            return (<MSTRIcon type={node.subtype === 779 || node.subtype === 776 ? 'cube' : 'report'} />);
          }
          return (<Icon type="folder" />);
        });

        if (node.children) {
          return (
            <TreeNode
              title={node.name}
              key={key}
              id={node.id}
              selectable={!node.isLeaf}
              icon={icon}
              dataRef={node}
              isLeaf={!!node.isLeaf}
              disabled={node.emptyFolder}
              typeId={node.type}
            >
              {this.renderTreeNodes(node.children)}
            </TreeNode>
          );
        }
        const title = node.ancestors ? (
          <Tooltip
            placement="topLeft"
            mouseLeaveDelay={0}
            mouseEnterDelay={0.5}
            trigger={['hover']}
            overlay={(
              <div>
                <div className="tooltip-header">
                  <div className="tooltip-first-line">{t('Location')}</div>
                  <div>{t('Last Modified')}</div>
                </div>
                <div className="tooltip-description">
                  <div className="tooltip-first-line">{node.ancestors}</div>
                  <div>{`${moment(node.dateModified).format('l')} ${moment(node.dateModified).format('LTS')}`}</div>
                </div>

              </div>
)}
          >
            <span>{node.name}</span>
          </Tooltip>
        ) : node.name;
        return (
          <TreeNode
            title={title}
            id={node.id}
            key={key}
            selectable={!node.isLeaf}
            icon={icon}
            dataRef={node}
            isLeaf={node.isLeaf}
            disabled={node.emptyFolder}
            projectId={node.projectId || this.getProjectId(node.path)}
            typeId={node.type}
            subtypeId={node.subtype}
            className={`C${key}`}
          />
        );
      });
    }

    render() {
      let tree;
      const { searchName, searchTree, mstrTree } = this.state;
      const { onSelect, onTreeSelect, onTreeExpand, expandedKeys, selectedKeys } = this.props;
      if (searchName) {
        tree = (
          <Spin indicator={loadingIndicator} wrapperClassName="loading-indicator-tree" spinning={searchTree.length === 0} tip="Searching...">
            <List
              id="search-list"
              tabIndex={-1}
              width={257.66}
              height={448}
              rowCount={searchTree.length}
              rowHeight={24}
              onClick={onSelect}
              rowRenderer={this.rowRenderer}
            />
          </Spin>
        );
      } else {
        tree = (
          <Spin indicator={loadingIndicator} wrapperClassName="loading-indicator-tree" spinning={!mstrTree || mstrTree.length === 0} tip="Loading...">
            <DirectoryTree
              showIcon
              loadData={this.onLoadData}
              onSelect={onTreeSelect}
              onExpand={onTreeExpand}
              expandedKeys={expandedKeys}
              selectedKeys={selectedKeys}
              defaultExpandedKeys={expandedKeys}
              className="folder-tree"
            >
              {this.renderTreeNodes(mstrTree)}
            </DirectoryTree>
          </Spin>
        );
      }
      return (
        <Col span={24} className="folder-tree-container" tabIndex={0}>
          {tree}
        </Col>
      );
    }
}

FolderTree.contextType = StateContext;
export default FolderTree;
