import React, { Component } from 'react';
import { Form, Radio, Input, Button, Tooltip } from 'antd';

import '../style/Authentication.css';

import backendHelper from '../utilities/BackendHelper';
import EnvNameInput from './EnvNameInput';
import MainContainer from './MainContainer';
import StateContext, { modes } from '../StateContext';
import { disableFocusOnMouseClick } from '../utilities/Utilities';
import MSTRIcon from './MSTRIcon';

const FormItem = Form.Item;
const RadioGroup = Radio.Group;

const formItemLayout = {
  colon: false,
  required: false,
};

const REGEX_URL = /^[\s]*(http|https):\/\//;

export class AuthenticationForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      credentialsPotentiallyValid: true,
      isFormValid: false,
    };
  }

  componentDidMount() {
    const { updateStateContext } = this.context;
    const { form } = this.props;
    document.addEventListener('keydown', this.handleKeyDown);
    document.querySelector('.ant-radio-group').setAttribute('tabindex', 0);
    disableFocusOnMouseClick('#connectButton, .ant-radio-group');
    updateStateContext({ authForm: form });
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this.handleKeyDown, false);
  }

    handleKeyDown = (event) => {
      const { form } = this.props;
      if (document.activeElement === document.querySelector('.ant-radio-group')) {
        if (event.keyCode > 36 && event.keyCode < 41) {
          const loginMode = event.keyCode < 39 ? '1' : '16';
          const { setFieldsValue } = form;
          setFieldsValue({
            loginMode,
          });
        }
      }
    }

    handleCancel = () => {
      backendHelper.closeConnector();
    }

    onConnect = async () => {
      const { form: { validateFields }, tokenChecker, keepSessionAlive } = this.props;
      const validator = validateFields();
      validator.then(async (credentials) => {
        const { api } = this.context;
        credentials.envName = credentials.envName.trim();
        credentials.url = credentials.url.trim();
        credentials.url = this.urlChecker(credentials.url);
        this.setState({ loading: true, credentials });
        api.setCredentials(credentials);
        const sufficientIServerVersion = await api.checkIServerVersion();
        let authenticated = false;
        if (sufficientIServerVersion) {
          authenticated = await api.getToken();
        }
        if (authenticated) {
          await api.getSessionInfo();
          this.updateRecentProjects(credentials, api);
          tokenChecker();
          keepSessionAlive();
          backendHelper.onLogin(api.token, credentials);
        }
        this.setState({ loading: false });
      });
    }

    updateRecentProjects = (credentials, api) => {
      const { updateStateContext, suggestedEnvironments } = this.context;
      const { url, loginMode, username, envName } = credentials;
      let all = [...suggestedEnvironments.all];
      all = all.filter((el) => el.envName !== envName);
      all = [...all, { url, loginMode, username, envName }];
      backendHelper.updateEnvSuggestions(all);
      updateStateContext({ api, mode: modes.PROJECT_SELECTION, envName, suggestedEnvironments: { all, temp: all } });
    }

    onFieldEnter = () => {
      this.onConnect();
    }

    onUrlPressEnter = () => {
      document.querySelector('#username').focus();
    }

    urlChecker = (url) => (url.slice(-1) === '/' ? url : `${url}/`)

    render() {
      const { form } = this.props;
      const { getFieldDecorator } = form;
      const { loading } = this.state;
      const { t } = this.context;
      const { title } = document;

      return (
        <MainContainer>
          <div className="authentication main-content">
            <div className="authentication-header">
              <MSTRIcon type="mstr-logo" />
              <h1 tabIndex={-1}>{title}</h1>
            </div>
            <div className="authentication-form">
              <Form id="authForm" onSubmit={this.onConnect} layout="vertical">
                <EnvNameInput form={form} />
                <FormItem label={`${t('Environment URL')}*`} {...formItemLayout}>
                  {getFieldDecorator('url', {
                    rules: [{
                      required: true,
                      message: (
                        <div>
                          <MSTRIcon type="red-info" />
                          <span>{t('Enter a valid environment URL')}</span>
                        </div>),
                      pattern: REGEX_URL,
                    }],
                    validateTrigger: 'onBlur',
                  })(
                    <Input placeholder="https://<<MSTR Domain>>/MicroStrategyLibrary/" autoComplete="off" onPressEnter={this.onUrlPressEnter} />,
                  )}
                </FormItem>
                <div className="authentication-form-credentials">
                  <FormItem label="" {...formItemLayout}>
                    {getFieldDecorator('loginMode', {
                      initialValue: '1',
                    })(
                      <RadioGroup>
                        <Tooltip placement="bottomRight" title={t('Standard Authentication Mode')} mouseEnterDelay={1}>
                          <Radio value="1">Standard</Radio>
                        </Tooltip>
                        <Tooltip placement="bottomLeft" title={t('LDAP Authentication Mode')} mouseEnterDelay={1}>
                          <Radio value="16">LDAP</Radio>
                        </Tooltip>
                      </RadioGroup>,
                    )}
                  </FormItem>

                  <FormItem {...formItemLayout} wrapperCol={{ md: { span: 8 } }}>
                    {getFieldDecorator('username', {
                      rules: [{
                        required: true,
                        message: (
                          <div>
                            <MSTRIcon type="red-info" />
                            <span>{t('Please enter your username')}</span>
                          </div>),
                      }],
                    })(
                      <Input placeholder={t('Username')} onChange={this.onCredentialsChange} onPressEnter={this.onFieldEnter} />,
                    )}

                  </FormItem>
                  <FormItem {...formItemLayout} wrapperCol={{ md: { span: 8 } }}>
                    {getFieldDecorator('password', {
                      rules: [{ required: false }],
                    })(
                      <Input type="password" placeholder={t('Password')} onChange={this.onCredentialsChange} onPressEnter={this.onFieldEnter} />,
                    )}
                  </FormItem>
                </div>
              </Form>
              <Button type="primary" onClick={() => this.onConnect()} loading={loading} id="connectButton" className="ml-auto">{t('Log in with credentials')}</Button>
            </div>
          </div>
        </MainContainer>
      );
    }
}

AuthenticationForm.contextType = StateContext;

export default Form.create()(AuthenticationForm);
