/* eslint-disable */
import React from 'react';
import {Component} from 'react';
import {ObjectBrowser} from './object-browser';
import './folder-browser.css';
import {folderBrowserHelper} from './folder-browser-helper';
import {Row, Col} from 'antd';
import {DatasetProperties} from './dataset-properties'
import {LoadingText} from 'mstr-react-library';
import FolderTree from '../FolderTree';
import LeftPanelStwitcher from './left-panel-switcher'

import FolderContent from '../FolderContent';
import backendHelper from '../../utilities/BackendHelper';
import StateContext from '../../StateContext'
import TopBar from '../TopBar';

const SHOW_LEFT_PANEL_SWITCHER = false;
const CHUNKS = 1;
const panelKeys = ['application', 'type', 'certified', 'owner'];


export class FolderBrowser extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dataSource: [],
            loading: false,
            searchText: '',
            properties: {
                projects: [],
                owners: [],
                subtypes: [],
                opened: panelKeys,
                certified: undefined,
            },
            leftPanelType: 'properties',
            // leftPanelType: 'folder-tree',
            selectedDirectory: {
                type: '',
                id: '',
                projectId: '',
            },
            treeSelectedKeys: [],
            treeExpandedKeys: [],
            loadingText: '',
            nameCellMaxWidth: '180px',
        };
    }

    async componentDidMount() {
        const {reloadDataSource, updateStateContext, browsingSearchText} = this.context;
        this.loadDatasetProperties();
        let dataSource;
        if (reloadDataSource) {
            dataSource = await this.loadDataSource();
            updateStateContext({dataSource});
        }
        else {
            dataSource = this.context.dataSource;
        }
        this.setState({dataSource, searchText: browsingSearchText}, () => this.setWidthOfNameCell());
        updateStateContext({reloadDataSource: true});
    }

    onChangeSwitch = () => {
        this.setState({
            isSmartFoldersView: !this.state.isSmartFoldersView,
        });
    }

    loadDatasetProperties = () => {
        const {envName, datasetProperties} = this.context;
        const item = datasetProperties.filter(e => e.envName === envName)[0];
        if (item) this.setState({properties: item.properties});
    }

    loadDataSource = async () => {
        const {api} = this.props;
        let {envUrl, token} = api;
        const {t, selectedProjects} = this.context;
        envUrl = envUrl + 'api'

        const loadingText = CHUNKS > 1 ? `${t('Loading...')} (0%)` : `${t('Loading...')}`;
        this.setState({loading: true, loadingText: loadingText});

        const projectList = await api.getProjectsForBrowsing(envUrl, token).then((projectList) => {
            return projectList;
        });

        const numberOfDatasets = CHUNKS > 1 ? await api.getNumberOfElements(envUrl, token, 3) : -1;

        let dataSource = [];
        const limit = Math.ceil(numberOfDatasets / CHUNKS);

        for (let i = 0; i < CHUNKS; i++) {
            const offset = i * limit;
            const percentage = ((i / CHUNKS) * 100).toFixed(0);
            const loadingText = CHUNKS > 1 ? `${t('Loading...')} (${percentage}%)` : `${t('Loading...')}`;
            this.setState({loadingText});
            let intermediateResult = await api.getLimitedSmartFolderContent(envUrl, token, 3, offset, limit);
            intermediateResult = intermediateResult.filter(e => api.supportedTypes.includes(e.subtype));
            intermediateResult = intermediateResult.filter(e => selectedProjects.includes(e.projectId));

            const parsedContent = intermediateResult
                ? folderBrowserHelper
                    .mapJsonToTableData(intermediateResult, projectList)
                : [];

            dataSource = [...dataSource, ...parsedContent];
        }

        this.setState({loading: false});
        return dataSource;
    };

    changeProperties = (newProperties) => {
        const properties = {...this.state.properties, ...newProperties};
        const {envName} = this.context;
        const obj = {envName, properties};
        let datasetProperties = [...this.context.datasetProperties.filter(e => e.envName !== envName)]
        datasetProperties = [...datasetProperties, obj]
        this.setState({properties});
        this.context.updateStateContext({datasetProperties})
        backendHelper.updateDatasetProperties(datasetProperties)
    }

    filterDataSource = (omitArray = []) => {
        const {properties, dataSource, searchText} = this.state;
        const {owners, subtypes, certified, projects} = properties;
        let filteredDataSource = [...dataSource];
        if (projects.length > 0 && !omitArray.includes('projects')) filteredDataSource = filteredDataSource.filter(e => projects.includes(e.projectId));
        if (owners.length > 0 && !omitArray.includes('owners')) filteredDataSource = filteredDataSource.filter(e => owners.includes(e.owner));
        if (subtypes.length > 0 && !omitArray.includes('subtypes')) filteredDataSource = filteredDataSource.filter(e => subtypes.includes(e.subtype));
        if (certified !== undefined && !omitArray.includes('certified')) filteredDataSource = filteredDataSource.filter(e => e.isCertified === certified);
        filteredDataSource = filteredDataSource.filter(e => e.name.toLowerCase().includes(searchText.toLowerCase()))

        return filteredDataSource;
    }

    onSearchChange = (e) => {
        const searchText = e.target.value;
        this.setState({searchText});
        this.context.updateStateContext({browsingSearchText: searchText})
    }

    switchLeftPanel = (leftPanelType) => {
        this.setState({leftPanelType, selectedDirectory: {type: '', id: '', projectId: ''}})
    }

    onContentDoubleClick = (name, id, projectId, type, treeKey) => {
        if (type === 'project') projectId = id;
        const treeSelectedKeys = [treeKey];
        const treeExpandedKeys = [...this.state.treeExpandedKeys, treeKey]
        this.setState({selectedDirectory: {type, id, projectId}, treeSelectedKeys, treeExpandedKeys})
    }


    onTreeSelect = (selectedKeys, e) => {
        const {eventKey} = e.node.props;
        this.setState((state) => {
            return {
                treeExpandedKeys: [...state.treeExpandedKeys, eventKey]
            }
        })

        const treeSelectedKeys = [e.node.props.eventKey];
        const pos = e.node.props.pos.split('-')[1]
        const projectId = this.context.selectedProjects[pos]
        const {id, type} = e.node.props.dataRef;
        const typeName = type ? 'folder' : 'project';
        this.setState({selectedDirectory: {type: typeName, id, projectId}, treeSelectedKeys})
    }

    onTreeExpand = (keys, event) => {
        const {expanded} = event;
        const {eventKey} = event.node.props;
        let treeExpandedKeys = [...this.state.treeExpandedKeys]
        if (expanded) {
            treeExpandedKeys = [...treeExpandedKeys, eventKey]
        }
        else {
            treeExpandedKeys = treeExpandedKeys.filter(e => e !== eventKey);
        }
        this.setState({treeExpandedKeys});
    }

    setWidthOfNameCell = () => {
        // remove injected inline style on windows
        const tableHeader = document.querySelector(".ant-table-header");
        tableHeader.removeAttribute('style');
        Array.from(document.querySelectorAll(".ant-table-fixed")).forEach(e => e.removeAttribute('style'));

        //Set appropriate max-width to the cells with dataset names
        const nameHeaderCell = document.querySelector(".name-column");
        let nameCellMaxWidth = `${nameHeaderCell.clientWidth - 20}px`;
        this.setState({nameCellMaxWidth})

        window.addEventListener("resize", () => {
            nameCellMaxWidth = `${nameHeaderCell.clientWidth - 20}px`;
            this.setState({nameCellMaxWidth})
        })
    }

    render() {
        const {onObjectChosen, onSorterChange, onSizeUpdated, children,
            chosenItem, scrollPosition, pageSize, sorter, onScrollUpdated, t, api} = this.props;

        return (
            <Row className="full-height">
                <TopBar onSearch={this.onSearchChange} searchText={this.state.searchText} />

                {this.state.loading &&
                    <div className="center-wrapper"><LoadingText text={this.state.loadingText} /></div>
                }

                {!this.state.loading &&
                    <Row className="browser-content import">
                        <Col className="full-height left-panel">
                            <div className="left-panel-view-wrapper">
                                {this.state.leftPanelType === 'properties' &&
                                    <DatasetProperties
                                        dataSource={this.state.dataSource}
                                        filteredDataSource={this.filterDataSource()}
                                        filteredDataSourceOwnersOmmited={this.filterDataSource(['owners'])}
                                        filteredDataSourceProjectsOmmited={this.filterDataSource(['projects'])}
                                        filteredDataSourceSubtypesOmmited={this.filterDataSource(['subtypes'])}
                                        filteredDataSourceCertifiedOmmited={this.filterDataSource(['certified'])}
                                        updateTrigger={this.onTypeClick}
                                        changeProperties={this.changeProperties}
                                        handleBack={this.props.handleBack}
                                        properties={this.state.properties} />
                                }
                                {this.state.leftPanelType === 'folder-tree' &&
                                    <FolderTree
                                        mstrApi={this.context.api}
                                        onTreeSelect={this.onTreeSelect}
                                        hideDatasets={true}
                                        selectedProjects={this.context.selectedProjects}
                                        onTreeExpand={this.onTreeExpand}
                                        expandedKeys={this.state.treeExpandedKeys}
                                        selectedKeys={this.state.treeSelectedKeys} />
                                }
                            </div>
                            {SHOW_LEFT_PANEL_SWITCHER &&
                                <LeftPanelStwitcher switcher={this.switchLeftPanel} panelType={this.state.leftPanelType} />}
                        </Col>
                        <Col className="full-height right-panel" tabIndex={-1}>
                            {this.state.leftPanelType === 'properties' &&
                                <ObjectBrowser
                                    onScrollUpdated={onScrollUpdated}
                                    onSizeUpdated={onSizeUpdated}
                                    sorter={sorter}
                                    onSorterChange={onSorterChange}
                                    key={this.state.selectedFolder}
                                    scrollPosition={scrollPosition}
                                    pageSize={pageSize}
                                    selectedItem={chosenItem || {}}
                                    dataSource={this.filterDataSource()}
                                    onObjectChosen={onObjectChosen}
                                    loading={this.state.loading}
                                    t={t}
                                    api={api}
                                    nameCellMaxWidth={this.state.nameCellMaxWidth} />
                            }
                            {this.state.leftPanelType === 'folder-tree' &&
                                <FolderContent
                                    mstrApi={this.context.api}
                                    onObjectChosen={onObjectChosen}
                                    selectedProjects={this.context.selectedProjects}
                                    selectedDirectory={this.state.selectedDirectory}
                                    onDoubleClick={this.onContentDoubleClick}
                                    selectableCubes
                                    selectableReports />
                            }
                        </Col>
                    </Row>}
                <Col span={24}>{children}</Col>
            </Row>
        );
    }
}

FolderBrowser.defaultProps = {
    t: text => text,
    onSearchChange: () => {},
    searchText: '',
};

FolderBrowser.contextType = StateContext;