import React, { Component } from 'react';
import { Button, Tooltip } from 'antd';
import { AttributeMetricFilter } from 'mstr-react-library';

import backendHelper, { backend } from '../utilities/BackendHelper';
import Footer from './Footer';
import MainContainer from './MainContainer';
import StateContext, { modes } from '../StateContext';
import { defineType } from '../utilities/MSTRFetch';
import { disableFocusOnMouseClick, debounce, sleep } from '../utilities/Utilities';

export default class DataPreparator extends Component {
  constructor(props) {
    super(props);

    this.state = {
      previewOpened: false,
      selectedOptions: 0,
      improperSetOfFilters: true,
      listenersAddedToFilterElements: false,
      radioButtonChanged: false,
    };
  }

  componentDidMount() {
    this.addEventsForResetingFilterContainerScroll();
    document.addEventListener('keydown', this.handleKeyDown);
    disableFocusOnMouseClick('button');

    this.onMouseMove = debounce(this.removeTitles, 100);
    document.addEventListener('mousemove', this.onMouseMove);
    this.replaceSwitchBar();
  }

  componentDidUpdate() {
    disableFocusOnMouseClick('button');
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this.handleKeyDown);
    document.removeEventListener('mousemove', this.onMouseMove);
  }

  removeTitles = () => {
    document.querySelectorAll('div[title]').forEach((e) => e.removeAttribute('title'));
  }


  addEventsForResetingFilterContainerScroll = () => {
    const filtersContainer = document.querySelector('.filter-panel-selectors > div:nth-child(3) .ant-col-12:nth-child(1)');
    filtersContainer.addEventListener('mouseenter', () => {
      const filterElements = [...document.querySelectorAll('.filter-panel-selectors > div:nth-child(3) .ant-col-12:nth-child(1) li')];
      if (filterElements.length > 0) {
        filterElements.forEach((e) => {
          e.addEventListener('click', (event) => {
            const filterTitle = e.target.querySelector('.filter-title');
            if (filterTitle) {
              event.target.querySelector('.filter-title').click();
              const filterElementsContainer = document.querySelector('.filter-panel-selectors > div:nth-child(3) .ant-col-12:nth-child(2) div[aria-label]');
              if (filterElementsContainer !== null) filterElementsContainer.scrollTop = 0;
            }
          });
        });
      }
    });
  }

  replaceSwitchBar = () => {
    const container = document.querySelector('.content .filter-panel-container');
    const switchBar = container.querySelector('.switch-bar');
    container.removeChild(switchBar);
    container.prepend(switchBar);
  }

  handleBack = () => {
    const { updateStateContext } = this.context;
    updateStateContext({ reloadDataSource: false, mode: modes.BROWSING });
  }

  handleImport = () => {
    const { api, selectedDataset } = this.context;
    this.setState({ improperSetOfFilters: true });
    backendHelper.sendDatasetInfo(api, selectedDataset);
  }

  onParametersChange = (attributes, metrics, filters) => {
    const { updateStateContext } = this.context;
    const length = attributes.length + metrics.length;
    const { selectedDataset } = this.context;
    selectedDataset.parameters = { attributes, metrics, filters, length };
    const improperSetOfFilters = length < 1;
    this.setState({ improperSetOfFilters });
    updateStateContext({ selectedDataset });
  };

  togglePreview = async () => {
    this.setState((state) => ({ previewOpened: !state.previewOpened }));
    await sleep(1);
    disableFocusOnMouseClick('.ant-modal-close');
  }

  handleKeyDown = (e) => {
    this.removeTabIndexes();
    this.removeTitles();
    if (e.keyCode === 13 || e.keyCode === 32) {
      this.handleSpaceOrEnter(e);
    }
  }

  removeTabIndexes = () => {
    document.querySelectorAll('.selector-list').forEach((e) => e.setAttribute('tabindex', -1));
    document.querySelectorAll('.checkbox-list').forEach((e) => e.setAttribute('tabindex', -1));
    document.querySelectorAll('.filter-title').forEach((e) => e.setAttribute('tabindex', -1));
    const filterElements = document.querySelectorAll('.filter-list ul li .ant-tree-node-content-wrapper');

    filterElements.forEach((e) => {
      e.setAttribute('tabindex', 0);
    });
    this.setState({ listenersAddedToFilterElements: true });
  }

  handleSpaceOrEnter = async (e) => {
    const filterTitleElement = e.target.querySelector('.filter-title');
    if (filterTitleElement) {
      filterTitleElement.click();
      await sleep(1);
      const filterElementsContainer = document.querySelectorAll('.checkbox-list')[2];
      filterElementsContainer.focus();
    }
  }

  render() {
    const { selectedDataset, api, t, importOrExportInProgress } = this.context;
    const { objectId, name, subtype, projectId } = selectedDataset;
    const { improperSetOfFilters, previewOpened } = this.state;
    const session = {
      USE_PROXY: false,
      url: `${api.envUrl}api`,
      authToken: api.token,
      projectId: selectedDataset.projectId,
    };

    const mstrData = {
      reportId: selectedDataset.objectId,
      reportSubtype: selectedDataset.subtype,
    };

    const importDisabled = improperSetOfFilters || importOrExportInProgress;
    const isRStudio = backend === 'RStudio';

    const subtypeName = defineType(subtype) === 'cube' ? 'Dataset' : 'Report';
    const title = `${t(`Import ${subtypeName}`)} > ${name}`;
    const previewTooltipTitle = improperSetOfFilters
      ? t('This button is currently disabled because you didn\'t select any data')
      : null;

    const importTooltipTitle = importOrExportInProgress
      ? isRStudio
        ? null
        : t('This button is disabled because another dataset is being currently fetched or exported.')
      : improperSetOfFilters
        ? t('This button is currently disabled because you didn\'t select any data')
        : null;

    return (
      <MainContainer>
        <AttributeMetricFilter
          key={objectId}
          session={session}
          reportId={objectId}
          reportSubtype={subtype}
          withFolderTree={false}
          withDataPreview
          projectId={projectId}
          onParametersChange={this.onParametersChange}
          openModal={previewOpened}
          closeModal={this.togglePreview}
          title={title}
          t={t}
          mstrData={mstrData}
        />
        <Footer
          left={
            <Button id="back" onClick={this.handleBack}>{t('Back')}</Button>
                  }
          right={(
            <div>
              <Tooltip placement="topRight" title={previewTooltipTitle} mouseEnterDelay={1} overlayClassName="info-tooltip">
                <Button id="data-preview" onClick={this.togglePreview} disabled={improperSetOfFilters}>{t('Data Preview')}</Button>
              </Tooltip>
              <Tooltip placement="topRight" title={importTooltipTitle} mouseEnterDelay={1} overlayClassName="info-tooltip">
                <Button id="import" onClick={this.handleImport} type="primary" disabled={importDisabled}>{t('Import')}</Button>
              </Tooltip>
            </div>
)}
        />
      </MainContainer>
    );
  }
}

DataPreparator.contextType = StateContext;
