/* eslint-disable jsx-a11y/label-has-associated-control */
/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
/* eslint-disable jsx-a11y/no-noninteractive-tabindex */
/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/no-static-element-interactions */
import React, { Component } from 'react';
import { Spin, Icon, Empty } from 'antd';

import './wrangler.css';
import '../../style/TopBar.scss';
import '../../style/mstr-react.css';
import StateContext from '../../StateContext';
import { containsSpecialCharacters } from '../../utilities/Utilities';

export default class DisplayBox extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selected: 0,
      draggedOver: '',
      nameChangePrep: '',
    };
  }

  componentWillReceiveProps() {
    const { title } = this.props;
    const allChecked = document.getElementById(`${title.toLowerCase()}-main-list`).querySelectorAll('input[name]:checked');
    this.setState({ selected: allChecked.length });
  }

  componentDidUpdate() {
    const { colNameEdit } = this.props;
    const box = colNameEdit.editColRef.current;
    if (box) {
      box.focus();
      box.select();
      box.addEventListener('keydown', this.handleBoxKeyDown);
      box.addEventListener('blur', this.handleBoxBlur);
    }
  }

  handleBoxKeyDown = (event) => {
    const { nameChangePrep } = this.state;
    if (event.keyCode === 27) { // esc
      this.setState({ nameChangePrep: '' });
    } else if (event.keyCode === 13) { // enter
      this.columnNameChangeHandler(nameChangePrep, false);
    }
  }

  handleBoxBlur = () => {
    const { nameChangePrep } = this.state;
    if (nameChangePrep) {
      this.columnNameChangeHandler(nameChangePrep, false);
    }
  }


  handleDragStart = async (event, item) => {
    const { parent } = this.props;
    parent.onDragDispBoxStart();
    await parent.setState({ dragged: { value: item, source: 'DB', isDragging: true } });
  }

  handleDragEnd = async () => {
    const { parent } = this.props;
    parent.onDragDispBoxEnd();
    await this.setState({ draggedOver: '' });
    await parent.setState({ dragged: {} });
  }

  handleDragDrop = async (event) => {
    const { title, parent } = this.props;
    parent.onDragDropHandler(event, title);
    await this.setState({ draggedOver: '' });
  }

  handleDragOverOtherItem = async (event, item) => {
    event.preventDefault();
    const { content, parent } = this.props;
    await this.setState({ draggedOver: item });
    await parent.setState({ dropPosition: content.indexOf(item) });
  }

  handleDragLeaveOtherItem = async (event) => {
    event.preventDefault();
    const { parent } = this.props;
    await this.setState({ draggedOver: '' });
    await parent.setState({ dropPosition: -1 });
  }

  handleItemClick = () => {
    const { title, content } = this.props;
    const allChecked = document.getElementById(`${title.toLowerCase()}-main-list`).querySelectorAll('input[name]:checked');
    const selectAll = document.getElementById(`${title.toLowerCase()}-select-all`);
    if (selectAll) selectAll.checked = allChecked.length === content.length;
    this.setState({ selected: allChecked.length });
  }

  handleSelectAll = () => {
    const { title, content } = this.props;
    const selectAllBoolean = document.getElementById(`${title.toLowerCase()}-select-all`).checked;
    document.getElementById(`${title.toLowerCase()}-main-list`).querySelectorAll('input[name]').forEach((el) => { el.checked = selectAllBoolean; });
    if (selectAllBoolean) {
      this.setState({ selected: content.length });
    } else {
      this.setState({ selected: 0 });
    }
  }

  columnNameChangeHandler = async (item, start) => {
    const { dataframeContent } = this.context;
    const { colNameEdit } = this.props;
    if (start) {
      colNameEdit.changeNext(true);
      await this.setState({ nameChangePrep: item });
    } else if (item && !start && colNameEdit.editColRef.current) {
      const { nameChangePrep } = this.state;
      const box = colNameEdit.editColRef.current;
      box.value = box.value.trim();
      const base = dataframeContent.content.actual[0];
      if (containsSpecialCharacters(box.value)) {
        colNameEdit.displayColNameSpecialChars();
        box.focus();
        return;
      }
      if (Object.keys(base).indexOf(box.value) >= 0 && box.value !== nameChangePrep) {
        colNameEdit.displayColNameAlreadyExists(box.value);
        box.focus();
        return;
      }
      if (box.value !== nameChangePrep && box.value.length > 0) { colNameEdit.applyColNameChange(colNameEdit.df, nameChangePrep, box.value); }
      colNameEdit.changeNext(false);
      await this.setState({ nameChangePrep: '' });
      box.removeEventListener('blur', this.handleBoxBlur);
      box.removeEventListener('keydown', this.handleBoxKeyDown);
    }
  }

  render() {
    const { t } = this.context;
    const { title, content, icon, dragging, loading, filter, colNameEdit, reference } = this.props;
    const { selected, draggedOver, nameChangePrep } = this.state;

    const list = [];
    let toShow = false;
    let element;
    if (content && content[0]) {
      content.forEach((el) => {
        toShow = !filter || el.toLowerCase().indexOf(filter) >= 0;
        element = (
          nameChangePrep === el
            // eslint-disable-next-line jsx-a11y/no-autofocus
            ? <input type="text" id={`name-change-${el.toLowerCase()}`} className="new-col-name-box" defaultValue={el} placeholder={t('Enter a new name...')} autoFocus maxLength={245} ref={colNameEdit.editColRef} />
            : <span tabIndex={0} onKeyUp={(key) => { key.key === 'Enter' && this.columnNameChangeHandler(el, true); }} onDoubleClick={() => { this.columnNameChangeHandler(el, true); }}>{el}</span>
        );
        list.push(
          <li
            title={el}
            className={(!toShow ? 'hidden-obj ' : '') + (draggedOver === el && dragging.dragProperSource ? 'dragged-over ' : '')}
            onClick={this.handleItemClick}
            data-group="drag-over-target"
            key={el}
          >
            <label
              className="checkbox"
              draggable
              name={el}
              key={el}
              onDragStart={(ev) => { this.handleDragStart(ev, el); }}
              onDragEnd={(ev) => { this.handleDragEnd(ev); }}
            >
              <input type="checkbox" name={el} id={`${el.toLowerCase()}-list-element`} />
              {icon}
              {element}
            </label>
          </li>,
        );
      });
      if (content.length > 0) { list.unshift(
        <li onClick={this.handleSelectAll} key={`${title.toLowerCase()}-select-all`}>
          <label className="checkbox">
            <input type="checkbox" id={`${title.toLowerCase()}-select-all`} />
            {icon}
            <span>{`(${t('All')})`}</span>
          </label>
        </li>,
      ); }
    }

    return (
      <div className="displaybox-holder">
        <div className="selector-title">
          {t(title)}
          {' '}
(
          {selected}
/
          {content.length}
)
        </div>
        <div
          className={`displaybox-content${(dragging.awaiting && dragging.dragProperSource ? ' await-drop' : '')}`}
          data-group="drop-option"
          ref={reference}
        >
          <ul id={`${title.toLowerCase()}-main-list`} className={`main-list${(!(content && content[0]) ? ' loader-holder' : '')}`} key={nameChangePrep}>
            {
                          loading
                          && <Spin indicator={<Icon type="loading" spin />} />
                      }
            {
                          !loading && content.length === 0
                          && <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />
                      }
            {
                          !loading && content.length > 0
                          && list
                      }
          </ul>
        </div>
      </div>
    );
  }
}

DisplayBox.contextType = StateContext;
