import React from 'react';
import {
  Attribute,
  Cube,
  CubeNew,
  ReportNew,
  Environment,
  Filter,
  FolderCollapsed,
  FolderExpanded,
  Metric,
  Report,
  ProjectCollapsed,
  ProjectExpanded,
  ListViewActive,
  ListViewDisabled,
  TreeViewActive,
  TreeViewDisabled,
  EmptyFolderNoResults,
  MSTRLogo,
  RedInfo,
} from './IconSource';


function MSTRIcon({ type }) {
  let icon;
  let customClass = 'hidden';
  switch (type) {
    case 'attribute':
      icon = <Attribute />;
      customClass = 'selector';
      break;
    case 'cube':
      icon = <Cube />;
      customClass = 'dataset';
      break;
    case 'environment':
      icon = <Environment />;
      customClass = 'environment';
      break;
    case 'filter':
      icon = <Filter />;
      customClass = 'selector';
      break;
    case 'folder-collapsed':
      icon = <FolderCollapsed />;
      break;
    case 'folder-expanded':
      icon = <FolderExpanded />;
      break;
    case 'metric':
      icon = <Metric />;
      customClass = 'selector';
      break;
    case 'project-collapsed':
      icon = <ProjectCollapsed />;
      customClass = 'selector';
      break;
    case 'project-expanded':
      icon = <ProjectExpanded />;
      customClass = 'selector';
      break;
    case 'report':
      icon = <Report />;
      customClass = 'dataset';
      break;
    case 'tree-view-disabled':
      icon = <TreeViewDisabled />;
      customClass = 'tree-view disabled';
      break;
    case 'tree-view-active':
      icon = <TreeViewActive />;
      customClass = 'tree-view active';
      break;
    case 'list-view-disabled':
      icon = <ListViewDisabled />;
      customClass = 'list-view disabled';
      break;
    case 'list-view-active':
      icon = <ListViewActive />;
      customClass = 'list-view active';
      break;
    case 'report-new':
      icon = <ReportNew />;
      customClass = 'report-new';
      break;
    case 'cube-new':
      icon = <CubeNew />;
      customClass = 'cube-new';
      break;
    case 'empty-folder-no-results':
      icon = <EmptyFolderNoResults />;
      customClass = 'empty-folder-no-results';
      break;
    case 'mstr-logo':
      icon = <MSTRLogo />;
      customClass = 'mstr-logo';
      break;
    case 'red-info':
      icon = <RedInfo />;
      customClass = 'red-info';
      break;
    default:
      break;
  }
  return (
    <span className={`mstr-icon ${customClass}`}>
      {icon}
    </span>
  );
}

export default MSTRIcon;
