/* eslint-disable new-cap */
import React, { Component } from 'react';
import { Row, Col, Button, Tooltip, Spin, Icon, message } from 'antd';

import MainContainer from './MainContainer';
import StateContext, { modes } from '../StateContext';
import Footer from './Footer';
import TopBarWrangler from './wrangler-components/TopBarWrangler';
import backendHelper, { backend } from '../utilities/BackendHelper';
import DisplayBox from './wrangler-components/DisplayBox';
import { simpleCompareIgnoreCase, simpleCopySortFromOtherObject, containsSpecialCharacters } from '../utilities/Utilities';
import { Attribute, Metric } from './IconSource';

export default class DataWrangler extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedDataFrame: '',
      loading: false,
      awaiter: 0,
      metrics: [],
      attributes: [],
      searchValue: '',
      dragged: null,
      draggedOver: '',
      dropPosition: -1,
      nameChangePrep: '',
      disabledNext: false,
    };
    this.editColRef = React.createRef();
    this.dragDrop = {
      prevTable: {
        thead: React.createRef(),
      },
      dispBox: {
        attr: React.createRef(),
        metr: React.createRef(),
      },
      components: {
        attr: React.createRef(),
        metr: React.createRef(),
      },
    };
  }

  componentDidMount() {
    const { selectedDataframes } = this.context;
    if (!(!selectedDataframes || selectedDataframes.length < 1)) {
      this.selectItem(this.selectedDataframes[0]);
      this.finalMetrAttrChanges = {};
      this.selectedDataframes.forEach((dataFrame) => { this.finalMetrAttrChanges[dataFrame] = { toMetrics: [], toAttributes: [] }; });
      this.clicked = false;
      this.thSize = 0;
    }
  }

  componentDidUpdate() {
    const box = this.editColRef.current;
    if (box) {
      box.focus();
      box.select();
      box.addEventListener('keydown', this.handleBoxKeyDown);
      box.addEventListener('blur', this.handleBoxBlur);
    }
  }

    handleBoxKeyDown = (event) => {
      const { nameChangePrep } = this.state;
      if (event.keyCode === 27) { // esc
        this.setState({ nameChangePrep: '' });
      } else if (event.keyCode === 13) { // enter
        this.setState({ recentlyEditedField: true }, () => this.columnNameChangeHandler(nameChangePrep, false));
      }
    }

    handleBoxBlur = () => {
      const { nameChangePrep } = this.state;
      if (nameChangePrep) {
        this.columnNameChangeHandler(nameChangePrep, false);
      }
    }

    onDragDispBoxStart = () => {
      const allRefs = this.dragDrop.dispBox;
      const allComp = this.dragDrop.components;
      for (const ref of Object.keys(allRefs)) {
        this.onDragStartApplyEvents(allRefs[ref].current, allComp[ref].current);
      }
    }

    onDragDispBoxEnd = () => {
      const allRefs = this.dragDrop.dispBox;
      const allComp = this.dragDrop.components;
      for (const ref of Object.keys(allRefs)) {
        this.onDragEndApplyEvents(allRefs[ref].current, allComp[ref].current);
      }
    }

    onDragStartApplyEvents = (reference, component) => {
      const dragOverFuncForRef = (ev) => { this.onAllowDropHandler(ev); };
      const dropFunc = (ev) => { component.handleDragDrop(ev); };
      const dragLeaveFunc = (ev) => { component.handleDragLeaveOtherItem(ev); };

      reference.addEventListener('dragover', dragOverFuncForRef);
      reference.addEventListener('drop', dropFunc);
      const elements = reference.querySelectorAll('li[data-group="drag-over-target"]');
      for (const key of Object.keys(elements)) {
        const dragOverFuncForElems = (ev) => { component.handleDragOverOtherItem(ev, elements[key].title); };
        elements[key].addEventListener('dragover', dragOverFuncForElems);
        elements[key].addEventListener('dragleave', dragLeaveFunc);
      }
    }

    onDragEndApplyEvents = (reference, component) => {
      const dragOverFuncForRef = (ev) => { this.onAllowDropHandler(ev); };
      const dropFunc = (ev) => { component.handleDragDrop(ev); };
      const dragLeaveFunc = (ev) => { component.handleDragLeaveOtherItem(ev); };

      reference.removeEventListener('dragover', dragOverFuncForRef);
      reference.removeEventListener('drop', dropFunc);
      const elements = reference.querySelectorAll('li[data-group="drag-over-target"]');
      for (const key of Object.keys(elements)) {
        const dragOverFuncForElems = (ev) => { component.handleDragOverOtherItem(ev, elements[key].title); };
        elements[key].removeEventListener('dragover', dragOverFuncForElems);
        elements[key].removeEventListener('dragleave', dragLeaveFunc);
      }
    }

    onDragDropHandler = async (event, target) => {
      event.preventDefault();

      await this.setState({ loading: true });

      const { metrics, attributes, dragged, selectedDataFrame, dropPosition } = this.state;
      const finalChange = this.finalMetrAttrChanges[selectedDataFrame];

      if (!!target && !!dragged) {
        const attrElIndex = attributes.indexOf(dragged.value);
        const metrElIndex = metrics.indexOf(dragged.value);
        const finalToMetrElIndex = finalChange.toMetrics.indexOf(dragged.value);
        const finalToAttrElIndex = finalChange.toAttributes.indexOf(dragged.value);

        switch (target) {
          case 'Attributes':
            if (metrElIndex >= 0) { metrics.splice(metrElIndex, 1); }
            if (attrElIndex >= 0) { attributes.splice(attrElIndex, 1); }
            if (dropPosition < 0) { attributes.push(dragged.value); } else { attributes.splice(dropPosition, 0, dragged.value); }
            if (finalToMetrElIndex >= 0) { finalChange.toMetrics.splice(finalToMetrElIndex, 1); }
            if (finalToAttrElIndex < 0) { finalChange.toAttributes.push(dragged.value); }
            break;
          case 'Metrics':
            if (attrElIndex >= 0) { attributes.splice(attrElIndex, 1); }
            if (metrElIndex >= 0) { metrics.splice(metrElIndex, 1); }
            if (dropPosition < 0) { metrics.push(dragged.value); } else { metrics.splice(dropPosition, 0, dragged.value); }
            if (finalToAttrElIndex >= 0) { finalChange.toAttributes.splice(finalToAttrElIndex, 1); }
            if (finalToMetrElIndex < 0) { finalChange.toMetrics.push(dragged.value); }
            break;
          default: break;
        }
      }

      backendHelper.reorderColumns(selectedDataFrame, attributes.concat(metrics), 1);
      this.finalMetrAttrChanges[selectedDataFrame] = finalChange;
      await this.setState({ metrics, attributes, dragged: null, dropPosition: -1, loading: false });
      this.clickSwitch();
    }

    onAllowDropHandler = (event) => {
      event.preventDefault();
    }

    handleBack = () => {
      this.context.updateStateContext({ mode: modes.EXPORT_DF_SELECT });
    }

    handleNext = () => {
      this.context.updateStateContext({ dataframeWrangle: this.finalMetrAttrChanges, mode: modes.EXPORT_DIR_SELECT });
    }

    changeNext = (boolean) => {
      this.setState({ disabledNext: boolean });
    }

    handleMoveAttributeMetric = (changeAttrToMetr) => {
      const { attributes, metrics, selectedDataFrame } = this.state;
      const finalChange = this.finalMetrAttrChanges[selectedDataFrame];

      this.setState({ loading: true });

      if (changeAttrToMetr) { // attributes to metrics
        const items = document.getElementById('attributes-main-list').querySelectorAll('input[name]:checked');
        items.forEach((selectedCheckbox) => {
          const i = attributes.indexOf(selectedCheckbox.name);
          const fi = finalChange.toAttributes.indexOf(selectedCheckbox.name);
          attributes.splice(i, 1);
          if (fi >= 0) finalChange.toAttributes.splice(fi, 1);
          metrics.push(selectedCheckbox.name);
          finalChange.toMetrics.push(selectedCheckbox.name);
          selectedCheckbox.checked = false;
        });
      } else { // metrics to attributes
        const items = document.getElementById('metrics-main-list').querySelectorAll('input[name]:checked');
        items.forEach((selectedCheckbox) => {
          const i = metrics.indexOf(selectedCheckbox.name);
          const fi = finalChange.toMetrics.indexOf(selectedCheckbox.name);
          metrics.splice(i, 1);
          if (fi >= 0) { finalChange.toMetrics.splice(fi, 1); }
          attributes.push(selectedCheckbox.name);
          finalChange.toAttributes.push(selectedCheckbox.name);
          selectedCheckbox.checked = false;
        });
      }

      backendHelper.reorderColumns(selectedDataFrame, attributes.concat(metrics), 1);
      this.finalMetrAttrChanges[selectedDataFrame] = finalChange;
      this.setState({ attributes, metrics, dragged: null, loading: false });
      this.clickSwitch();
    }

    clickSwitch = () => {
      const switchButton = document.querySelector('#view-selected-switch');
      const checked = switchButton.getAttribute('aria-checked') === 'true';
      checked && switchButton.click();
    }

    waitForContentUpdate = (item) => {
      if (!!this.context.dataframeContent && this.context.dataframeContent.name === item) {
        this.prepareMetricsAttributesSelection();
        this.removeSpecialCharactersFromColumnNames();
      }
    }

    removeSpecialCharactersFromColumnNames = () => {
      const dataframeName = this.context.dataframeContent.name;
      const { attributes, metrics } = this.state;
      const allColumns = [...attributes, ...metrics];
      allColumns.forEach((columnName) => {
        if (containsSpecialCharacters(columnName)) {
          const newColumnName = this.removeSpecialCharacters(columnName);
          this.applyColNameChange(dataframeName, columnName, newColumnName);
        }
      });
    }

    removeSpecialCharacters = (str) => {
      const splitted = str.split('');
      for (let i = 0; i < splitted.length; i++) {
        const letter = splitted[i];
        const isSpecialCharacter = containsSpecialCharacters(letter);
        if (isSpecialCharacter) splitted[i] = '_';
      }
      const joined = splitted.join('');
      const multipleUnderscoresRemoved = joined.replace(/_+(?=_)/g, '');
      let final = multipleUnderscoresRemoved;
      if (final[0] === '_') final = final.substring(1);
      if (final[final.length - 1] === '_') final = final.substring(0, final.length - 1);
      return final;
    }

    prepareMetricsAttributesSelection = async () => {
      const isRStudio = backend === 'RStudio';
      const { types } = this.context.dataframeContent.content;
      const metrics = types.metrics.map((metric) => (isRStudio ? metric.name[0] : metric.name));
      const attributes = types.attributes.map((attribute) => (isRStudio ? attribute.name[0] : attribute.name));
      const data = this.context.dataframeContent.content.actual[0];
      const { selectedDataFrame } = this.state;
      const { finalMetrAttrChanges } = this;
      if (finalMetrAttrChanges[selectedDataFrame]) {
        finalMetrAttrChanges[selectedDataFrame].toMetrics.forEach((toMetricElement) => {
          const attributeIndex = attributes.indexOf(toMetricElement);
          if (attributeIndex >= 0) attributes.splice(attributeIndex, 1);
          if (metrics.indexOf(toMetricElement) < 0) metrics.push(toMetricElement);
        });
        finalMetrAttrChanges[selectedDataFrame].toAttributes.forEach((toAttributeElement) => {
          const metricIndex = metrics.indexOf(toAttributeElement);
          if (metricIndex >= 0) metrics.splice(metricIndex, 1);
          if (attributes.indexOf(toAttributeElement) < 0) attributes.push(toAttributeElement);
        });
        attributes.sort(simpleCopySortFromOtherObject(Object.keys(data)));
        metrics.sort(simpleCopySortFromOtherObject(Object.keys(data)));
      } else {
        backendHelper.reorderColumns(selectedDataFrame, attributes.concat(metrics), 1);
      }
      await this.setState({ metrics, attributes, loading: false });
    }

    selectItem = (item) => {
      backendHelper.onExportGatherDetails(item);
      clearInterval(this.state.awaiter);
      this.setState({ selectedDataFrame: item, loading: true, metrics: [], attributes: [], awaiter: setInterval(this.waitForContentUpdate, 100, item) });
    }

    searchBarHandler = (event) => {
      const val = event.target.value.toLowerCase();
      this.setState({ searchValue: val });
    }

    displayColNameAlreadyExists = (name) => {
      const { t } = this.context;
      const text = t('Column name `{{colName}}` already exists. Please try again.', { colName: name });
      message.destroy();
      message.warning(text, 3);
    }

    displayColNameSpecialChars = () => {
      const { t } = this.context;
      const text = t('New column name contains forbidden characters. Only letters, numbers, and some special characters ( % (  )[ ] - _ ) can be used. Please try again.');
      message.destroy();
      message.warning(text, 6);
    }

    applyColNameChange = (dataframeName, previousColName, newColName) => {
      const { metrics, attributes } = this.state;

      const metricIndex = metrics.indexOf(previousColName);
      const newMetricIndex = this.finalMetrAttrChanges[dataframeName].toAttributes.indexOf(previousColName);
      const attributeIndex = attributes.indexOf(previousColName);
      const newAttributeIndex = this.finalMetrAttrChanges[dataframeName].toMetrics.indexOf(previousColName);

      if (metricIndex >= 0) { metrics.splice(metricIndex, 1, newColName); }
      if (newMetricIndex >= 0) { this.finalMetrAttrChanges[dataframeName].toAttributes.splice(newMetricIndex, 1, newColName); }
      if (attributeIndex >= 0) { attributes.splice(attributeIndex, 1, newColName); }
      if (newAttributeIndex >= 0) { this.finalMetrAttrChanges[dataframeName].toMetrics.splice(newAttributeIndex, 1, newColName); }

      this.context.dataframeContent.content.actual.forEach((dataFrameRow) => {
        dataFrameRow[newColName] = dataFrameRow[previousColName];
        delete dataFrameRow[previousColName];
      });

      backendHelper.updateDataframeColumnName(dataframeName, previousColName, newColName);
      this.setState({ metrics, attributes });
    }

    columnNameChangeHandler = async (item, start) => {
      if (start) {
        this.thSize = document.getElementById(`${item}-col-head`).getBoundingClientRect().width;
        await this.setState({ nameChangePrep: item, disabledNext: true });
      } else if (item && !start) {
        const nameChangePrep = item;
        const { selectedDataFrame } = this.state;
        const boxId = `name-change-${nameChangePrep}`;
        const box = document.getElementById(boxId);
        box.value = box.value.trim();
        const base = this.context.dataframeContent.content.actual[0];
        if (containsSpecialCharacters(box.value)) {
          this.displayColNameSpecialChars();
          box.focus();
          return;
        }
        if (Object.keys(base).indexOf(box.value) >= 0 && box.value !== nameChangePrep) {
          this.displayColNameAlreadyExists(box.value);
          box.focus();
          return;
        }
        if (box.value !== nameChangePrep && box.value.length > 0) { this.applyColNameChange(selectedDataFrame, nameChangePrep, box.value); }
        this.thSize = 0;
        await this.setState({ nameChangePrep: '', disabledNext: false });
        box.removeEventListener('blur', this.handleBoxBlur);
        box.removeEventListener('keydown', this.handleBoxKeyDown);
      }
    }

    onDragStartHandler = async (event, item) => {
      const head = this.dragDrop.prevTable.thead.current;
      const elements = head.querySelectorAll('th');
      head.addEventListener('dragover', (ev) => { this.onAllowDropHandler(ev); });
      head.addEventListener('drop', (ev) => { this.onDragColumnDropHandler(ev); });
      for (const el of elements) {
        el.addEventListener('dragover', (ev) => { this.onDragOverHandler(ev, el.title); });
        el.addEventListener('dragleave', (ev) => { this.onDragLeaveHandler(ev); });
      }
      await this.setState({ dragged: { value: item, source: 'PT', isDragging: true } });
    }

    onDragEndHandler = async (event) => {
      const head = this.dragDrop.prevTable.thead.current;
      const elements = head.querySelectorAll('th');
      head.removeEventListener('dragover', (ev) => { this.onAllowDropHandler(ev); });
      head.removeEventListener('drop', (ev) => { this.onDragColumnDropHandler(ev); });
      for (const el of elements) {
        el.removeEventListener('dragover', (ev) => { this.onDragOverHandler(ev, el.title); });
        el.removeEventListener('dragleave', (ev) => { this.onDragLeaveHandler(ev); });
      }
      await this.setState({ dragged: null, dropPosition: -1, draggedOver: '' });
    }

    onDragOverHandler = async (event, item) => {
      event.preventDefault();
      const { attributes, metrics, dropPosition, draggedOver } = this.state;
      let position = attributes.indexOf(item);
      if (position < 0) { position = metrics.indexOf(item) + attributes.length; }
      if (item === '_last_place_') { position = attributes.length + metrics.length; }
      if (dropPosition !== position || draggedOver !== item) await this.setState({ dropPosition: position, draggedOver: item });
    }

    onDragLeaveHandler = async (event) => {
      event.preventDefault();
      await this.setState({ dropPosition: -1, draggedOver: '' });
    }

    onDragColumnDropHandler = async (event) => {
      event.preventDefault();
      const { dropPosition, dragged, attributes } = this.state;
      if (dragged.source === 'PT') {
        if (dropPosition < 0) {

        } else if (dropPosition < attributes.length) {
          this.onDragDropHandler(event, 'Attributes');
        } else {
          await this.setState({ dropPosition: dropPosition - attributes.length });
          this.onDragDropHandler(event, 'Metrics');
        }
      }
    }

    render() {
      const { t, selectedDataframes } = this.context;
      let list;
      try { list = this.context.dataframeContent.content.actual; } catch (e) { list = null; }
      const { metrics, attributes, loading, searchValue, nameChangePrep, selectedDataFrame, dragged, draggedOver } = this.state;
      const { dispBox } = this.dragDrop;
      const icon = {
        attribute: <Attribute />,
        metric: <Metric />,
      };
      const dragCalc = {
        awaiting: (dragged ? dragged.isDragging : false),
        dragProperSource: (dragged ? dragged.source === 'DB' : false),
      };
      const colNameEdit = {
        applyColNameChange: this.applyColNameChange,
        displayColNameAlreadyExists: this.displayColNameAlreadyExists,
        displayColNameSpecialChars: this.displayColNameSpecialChars,
        changeNext: this.changeNext,
        editColRef: this.editColRef,
        df: selectedDataFrame,
      };
      const dragProperSource = dragged ? dragged.source === 'PT' : false;

      this.selectedDataframes = selectedDataframes;

      if (this.state.awaiter !== 0 && !loading) { clearInterval(this.state.awaiter); }

      const items = [];
      this.selectedDataframes.sort(simpleCompareIgnoreCase)
        .forEach((dataFrame) => { items.push(<Button className={selectedDataFrame === dataFrame ? 'dataframe-item-selected' : 'dataframe-item'} onClick={() => this.selectItem(dataFrame)} key={dataFrame}>{dataFrame}</Button>); });

      const headings = [];
      const thStyle = {
        width: `${this.thSize}px`,
        padding: '0',
      };
      attributes.forEach((attrName) => {
        const cellContent = (
          <span>
            {icon.attribute}
            {attrName}
                    </span>
        );
        headings.push(
          <th
            title={attrName}
            id={`${attrName}-col-head`}
            key={`${attrName}-col-head`}
            className={draggedOver === attrName && dragProperSource ? 'th-dragged-over' : ''}
            tabIndex={0}
            style={nameChangePrep === attrName ? thStyle : {}}
            onKeyUp={(key) => { key.key === 'Enter' && this.columnNameChangeHandler(attrName, true); }}
            onDoubleClick={() => { nameChangePrep !== attrName && this.columnNameChangeHandler(attrName, true); }}
            draggable
            onDragStart={(ev) => { this.onDragStartHandler(ev, attrName); }}
            onDragEnd={(ev) => { this.onDragEndHandler(ev); }}
          >
            {nameChangePrep !== attrName && cellContent}
            {
                        nameChangePrep === attrName
                        && (
                        <input
                          type="text"
                          id={`name-change-${attrName}`}
                          className="new-col-name-box"
                          defaultValue={attrName}
                          placeholder={t('Enter a new name...')}
                          autoFocus
                          maxLength={245}
                          ref={this.editColRef}
                        />
                        )
                    }
          </th>,
        );
      });
      metrics.forEach((metricName) => {
        const cellContent = (
          <span>
            {icon.metric}
            {metricName}
                    </span>
        );
        headings.push(
          <th
            title={metricName}
            id={`${metricName}-col-head`}
            key={`${metricName}-col-head`}
            className={draggedOver === metricName && dragProperSource ? 'th-dragged-over' : ''}
            tabIndex={0}
            style={nameChangePrep === metricName ? thStyle : {}}
            onKeyUp={(key) => { key.key === 'Enter' && this.columnNameChangeHandler(metricName, true); }}
            onDoubleClick={() => { nameChangePrep !== metricName && this.columnNameChangeHandler(metricName, true); }}
            draggable
            onDragStart={(ev) => { this.onDragStartHandler(ev, metricName); }}
            onDragEnd={(ev) => { this.onDragEndHandler(ev); }}
          >
            {nameChangePrep !== metricName && cellContent}
            {
                        nameChangePrep === metricName
                        && (
                        <input
                          type="text"
                          id={`name-change-${metricName}`}
                          className="new-col-name-box"
                          defaultValue={metricName}
                          placeholder={t('Enter a new name...')}
                          autoFocus
                          maxLength={245}
                          ref={this.editColRef}
                        />
                        )
                    }
          </th>,
        );
      });

      const rows = [];
      if (list && list[0]) {
        for (let i = 0; i < list.length; i++) {
          rows[i] = [];
          for (let j = 0; j < attributes.length; j++) {
            rows[i].push(<td className={isNaN(list[i][attributes[j]].toString()) ? '' : 'align-right'} key={`${j + i * 2}`}>{list[i][attributes[j]]}</td>);
          }
          for (let j = 0; j < metrics.length; j++) {
            rows[i].push(<td className={isNaN(list[i][metrics[j]].toString()) ? '' : 'align-right'} key={`abc${j * 2 + i * 10}`}>{list[i][metrics[j]]}</td>);
          }
          rows[i] = <tr key={`row-${i}`}>{rows[i]}</tr>;
        }
      }

      return (
        <MainContainer>
          <Row className="full-height">
            <TopBarWrangler onSearch={this.searchBarHandler} />
            <Row className="wrangler-content content">
                <Col className="full-height left-panel align-top">
                    <div className="menu-title">{t('Data Frames')}</div>
                    {items}
                  </Col>
                <Col className="right-panel align-top">
                    <div className="inner-wrangler-content" key={nameChangePrep}>
                        <DisplayBox
                            title="Attributes"
                            content={attributes}
                            loading={loading}
                            icon={icon.attribute}
                            dragging={dragCalc}
                            parent={this}
                            filter={searchValue}
                            colNameEdit={colNameEdit}
                            reference={dispBox.attr}
                            ref={this.dragDrop.components.attr}
                          />
                        <div className="arrow-holder">
                            <Tooltip placement="top" trigger="hover" mouseEnterDelay={0.2} title={t('Convert selected Attributes to Metrics')}>
                                <Button id="button-attributes-to-metrics" onClick={() => { this.handleMoveAttributeMetric(true); }}>&rsaquo;</Button>
                              </Tooltip>
                            <Tooltip placement="bottom" trigger="hover" mouseEnterDelay={0.2} title={t('Convert selected Metrics to Attributes')}>
                                <Button id="button-metrics-to-attributes" onClick={() => { this.handleMoveAttributeMetric(false); }}>&lsaquo;</Button>
                              </Tooltip>
                          </div>
                        <DisplayBox
                            title="Metrics"
                            content={metrics}
                            loading={loading}
                            icon={icon.metric}
                            dragging={dragCalc}
                            parent={this}
                            filter={searchValue}
                            colNameEdit={colNameEdit}
                            reference={dispBox.metr}
                            ref={this.dragDrop.components.metr}
                          />
                      </div>
                    <div className="main-splitter" />
                    <div className="table-holder">
                        <div className={loading ? 'loader-holder' : ''}>
                            {
                                        loading
                                        && <Spin indicator={<Icon type="loading" spin />} />
                                    }
                            {
                                        !loading
                                        && (
                                        <table>
                                          <thead
                                            ref={this.dragDrop.prevTable.thead}
                                            className={dragged ? (dragged.isDragging && dragProperSource ? 'await-drop' : '') : ''}
                                          >
                                            <tr>{headings}</tr>

                                          </thead>
                                          <tbody>{rows}</tbody>
                                        </table>
                                        )
                                    }
                          </div>
                      </div>
                  </Col>
              </Row>
          </Row>
          <Footer
            left={<Button id="back" onClick={this.handleBack}>{t('Back')}</Button>}
            right={<Button id="next" onClick={this.handleNext} type="primary" disabled={this.state.disabledNext}>{t('Next')}</Button>}
          />
        </MainContainer>
      );
    }
}

DataWrangler.contextType = StateContext;
