import React, { Component } from 'react';
import { Input, Switch } from 'antd';

import './wrangler.css';
import '../../style/TopBar.scss';
import StateContext from '../../StateContext';

export default class TopBarWrangler extends Component {
  viewSelectedHandler = (checked) => {
    const child = document.querySelectorAll('.inner-wrangler-content .displaybox-holder input:not(:checked)');
    if (checked) {
      child.forEach((el) => { el.parentNode.parentNode.classList.add('hidden-obj'); });
    } else { child.forEach((el) => { el.parentNode.parentNode.classList.remove('hidden-obj'); }); }
  }

  render() {
    const { t } = this.context;
    const { onSearch } = this.props;
    return (
      <div className="top-bar">
        <div className="top-nav">
          <div className="wrangle-title">
            {`${t('Export Data')} > ${t('Wrangle Data')}`}
          </div>
        </div>
        <div className="top-bar__right">
          <div>
            <label className="switch-label" htmlFor="view-selected-switch">{t('View selected')}</label>
            <Switch id="view-selected-switch" onChange={(checked) => { this.viewSelectedHandler(checked); }} />
          </div>
          <div className="object-search">
            <Input.Search
              placeholder={`${t('Search')}...`}
              onChange={onSearch}
              className="project-chooser-search"
            />
          </div>
        </div>
      </div>
    );
  }
}

TopBarWrangler.contextType = StateContext;
