import React from 'react';
import { Tooltip } from 'antd';
import MSTRIcon from './MSTRIcon';

export default function FolderItem({ typeName, name, id, projectId, subtype, treeKey,
  onClick = () => null, selectable = false, selected = false,
  onDoubleClick = () => null, openable = false }) {
  let iconType;
  switch (typeName) {
    case ('project'):
      iconType = 'project-collapsed';
      break;
    case ('folder'):
      iconType = 'folder-collapsed';
      break;
    case ('report'):
      iconType = 'report-new';
      break;
    case ('cube'):
      iconType = 'cube-new';
      break;
    default:
      iconType = 'dataset-grid';
  }

  return (
    <Tooltip title={name} placement="top" mouseEnterDelay={1} overlayClassName="info-tooltip">
      <button
        className={`folder-item ${selectable ? 'selectable' : ''} ${selected ? 'selected' : ''} ${openable ? 'openable' : ''}`}
        onClick={() => (selectable ? onClick(name, id, projectId, subtype) : null)}
        onKeyDown={(e) => e.keyCode === 13 && openable && onDoubleClick(name, id, projectId, typeName, treeKey)}
        onDoubleClick={() => (openable ? onDoubleClick(name, id, projectId, typeName, treeKey) : null)}
        type="button"
      >
        <div className="icon">
          <MSTRIcon type={iconType} />
        </div>
        <div className="name">
          {name}
        </div>
      </button>
    </Tooltip>
  );
}
