/* eslint-disable camelcase */
import { supportedTypes } from './Utilities';

let Shiny;

if (window.Shiny) {
  Shiny = window.Shiny;
} else {
  Shiny = {
    setInputValue: (key, value) => {
      console.log('setInputValue:', key, value);
    },
  };
}

export default class RStudioHelper {
    onExportDataMode = () => {
      Shiny.setInputValue('onExportDataMode', Date.now());
    }

    onExportGatherDetails = (dataframe_name) => {
      Shiny.setInputValue('dataframeToGather', dataframe_name);
      Shiny.setInputValue('onExportGatherDetails', Date.now());
    }

    sendDatasetInfo = (api, selectedDataset) => {
      this.sendEnvironmentInfo(api);
      const { projectId, objectId, subtype, name, parameters } = selectedDataset;
      const datasetType = supportedTypes.reportTypes.includes(subtype) ? 'report' : 'dataset';

      const body = {
        attributes: [],
        metrics: [],
        filters: [],
      };
      if (parameters.length) {
        const { attributes, metrics, filters } = parameters;
        if (attributes && attributes.length) body.attributes = attributes;
        if (metrics && metrics.length) body.metrics = metrics;
        if (filters) {
          // eslint-disable-next-line no-restricted-syntax
          for (const key of Object.keys(filters)) {
            body.filters = [...body.filters, ...filters[key]];
          }
        }
      }

      Shiny.setInputValue('projectId', projectId);
      Shiny.setInputValue('datasetId', objectId);
      Shiny.setInputValue('datasetType', datasetType);
      Shiny.setInputValue('datasetName', name);
      Shiny.setInputValue('datasetBody', JSON.stringify(body));
      Shiny.setInputValue('triggerDatasetInfo', Date.now());
    };

    closeConnector = () => {
      Shiny.setInputValue('triggerClose', Date.now());
    };

    updateEnvSuggestions = (newEnvSuggestions) => {
      Shiny.setInputValue('newEnvSuggestions', JSON.stringify(newEnvSuggestions));
    };

    updateRecentProjects = (newRecentProjects) => {
      Shiny.setInputValue('newRecentProjects', JSON.stringify(newRecentProjects));
    };

    updateDatasetProperties = (properties) => {
      Shiny.setInputValue('updateDatasetProperties', JSON.stringify(properties));
    }

    updateDataframeColumnName = (dataframe_name, prev_col_name, new_col_name) => {
      Shiny.setInputValue('dataframeForColNameChange', dataframe_name);
      Shiny.setInputValue('previousColName', prev_col_name);
      Shiny.setInputValue('newColName', new_col_name);
      Shiny.setInputValue('onWrangleColNameChange', Date.now(), { priority: 'event' });
    }

    reorderColumns = (dataframe_name, columns_in_row, start_index) => {
      Shiny.setInputValue('dataframeForColReorder', dataframe_name);
      Shiny.setInputValue('columnsForReorder', JSON.stringify(columns_in_row));
      Shiny.setInputValue('reorderStartIndex', start_index);
      Shiny.setInputValue('onWrangleReorderRequest', Date.now());
    }

    sendMessageToConsole = (message) => {
      Shiny.setInputValue('msgToConsole', message);
    };

    // eslint-disable-next-line no-unused-vars
    notifyOnModeChange = (mode) => {}

    sendEnvironmentInfo = ({ username, password, loginMode, envUrl }) => {
      Shiny.setInputValue('envUrl', `${envUrl}api`);
      Shiny.setInputValue('loginMode', loginMode);
      Shiny.setInputValue('username', username);
      Shiny.setInputValue('password', password);
    }

    exportDataframes = (names, folderId, saveAsName, api, wrangle) => {
      this.sendEnvironmentInfo(api);
      Shiny.setInputValue('projectId', api.project);
      Shiny.setInputValue('names', names);
      Shiny.setInputValue('folderId', folderId);
      Shiny.setInputValue('saveAsName', saveAsName);
      Shiny.setInputValue('wrangle', JSON.stringify(wrangle));
      Shiny.setInputValue('triggerExportDataframe', Date.now());
    }

    onUserTooltipOpen = () => {}

    // eslint-disable-next-line no-unused-vars
    onLogin = (token, credentials) => {
    }
}
