/* eslint-disable */
import React, {Component} from 'react';

import {Collapse, Icon, Input, Checkbox, Tooltip} from 'antd';
import {compareByName, addOrRemoveItemToArray, addOrRemoveItemsToArray, simpleCompareIgnoreCase, disableFocusOnMouseClick} from '../../utilities/Utilities'

import StateContext from '../../StateContext';

const reportTypes = [768, 769, 774];
const datasetTypes = [776, 779];
const MAX_PROPS_FOR_OPENED_PANEL = 4;
const {Panel} = Collapse;

export class DatasetProperties extends Component {
    constructor(props) {
        super(props);
        this.state = {
            projects: [],
            owners: [],
            ownersTemp: [],
            ownerSearchText: '',
        };
    }

    componentDidMount() {
        this.listParameters();
        disableFocusOnMouseClick('.ant-collapse-header');
    }

    componentDidUpdate(prevProps) {
        if (this.props.dataSource !== prevProps.dataSource && this.props.dataSource.length > 0) {
            this.listParameters();
        }
    }

    changeProperties = (type, id) => {
        const items = addOrRemoveItemToArray(id, this.props.properties[type]);
        this.props.changeProperties({[type]: items});
    }

    listParameters = () => {
        const {dataSource, properties} = this.props;
        if (dataSource.length < 1) return;
        const owners = [...new Set(dataSource.map(e => e.owner))];

        let projects = [];
        dataSource.forEach(el => {
            const {projectId, applicationName} = el;
            const projectEl = projects.find(el => el.id === projectId);
            if (!projectEl) {
                projects.push({
                    name: applicationName,
                    id: projectId,
                });
            }
        });

        const {selectedProjects} = this.context;
        projects = projects.filter(e => selectedProjects.includes(e.id));
        this.setState({projects, owners, ownersTemp: owners});

        let opened = [...properties.opened];

        if (projects.length > MAX_PROPS_FOR_OPENED_PANEL) opened = opened.filter(e => e !== 'application');
        if (owners.length > MAX_PROPS_FOR_OPENED_PANEL) opened = opened.filter(e => e !== 'owner');
        this.props.changeProperties({opened})
    }

    toggleSubtypes = (type) => {
        const subtypesList = type === 'reports' ? reportTypes : datasetTypes;
        const subtypes = addOrRemoveItemsToArray(subtypesList, this.props.properties.subtypes);
        this.props.changeProperties({subtypes})
    }

    toggleCertified = () => {
        const certified = this.props.properties.certified ? undefined : true;
        this.props.changeProperties({certified});
    }

    onOwnerSearchChange = (e) => {
        const searchText = e.target.value.toLowerCase();
        let ownersTemp = [...this.state.owners];
        ownersTemp = ownersTemp.filter(e => e.toLowerCase().includes(searchText))
        this.setState({ownersTemp})
    }

    onCollapseChange = (opened) => {
        this.props.changeProperties({opened})
    }

    toggleAllOwners = () => {
        const allOwnersSelected = this.props.properties.owners.length === this.state.owners.length;
        const owners = allOwnersSelected ? [] : [...this.state.owners];
        const properties = {...this.props.properties, owners}
        this.props.changeProperties(properties);
    }

    render() {
        const {t} = this.context;
        const {properties, filteredDataSource, filteredDataSourceOwnersOmmited, filteredDataSourceProjectsOmmited, filteredDataSourceSubtypesOmmited,
            filteredDataSourceCertifiedOmmited} = this.props;
        let allOwnersAmount = filteredDataSource.length;

        const owners = this.state.ownersTemp.sort(simpleCompareIgnoreCase).map(owner => {
            const checked = properties.owners.includes(owner)
            const amount = filteredDataSourceOwnersOmmited.filter(e => e.owner === owner).length;
            if (amount < 1) return null;
            return <tr key={owner}><td><Checkbox onChange={() => this.changeProperties('owners', owner)} checked={checked} id={owner} /></td>
                <td title={owner}><label htmlFor={owner}>{owner}</label></td>
                <td><Tooltip placement="right" title={t('Number of reports or datasets created by user')} mouseEnterDelay={1} overlayClassName='info-tooltip one-liner'>
                    {amount}
                </Tooltip></td></tr>
        })

        const applications = this.state.projects.sort(compareByName).map(el => {
            const checked = properties.projects.includes(el.id);
            const amount = filteredDataSourceProjectsOmmited.filter(e => e.projectId === el.id).length;
            // allOwnersAmount += amount;
            return (<tr key={el.id}><td><Checkbox onChange={() => this.changeProperties('projects', el.id)} checked={checked} id={el.id} /></td>
                <td title={el.name}><label htmlFor={el.id}>{el.name}</label></td>
                <td><Tooltip placement="right" title={t('Number of available reports or datasets inside application')} mouseEnterDelay={1} overlayClassName='info-tooltip one-liner'>
                    {amount}
                </Tooltip></td></tr>)
        })

        const reportsChecked = properties.subtypes.includes(768)
        const datasetsChecked = properties.subtypes.includes(776)

        const allOwnersSelected = properties.owners.length === this.state.owners.length;
        const allOwnersIndeterminate = allOwnersSelected ? false : properties.owners.length > 0;

        const reportsAmount = filteredDataSourceSubtypesOmmited.filter(e => reportTypes.includes(e.subtype)).length;
        const datasetsAmount = filteredDataSourceSubtypesOmmited.filter(e => datasetTypes.includes(e.subtype)).length;

        const certifiedAmount = filteredDataSourceCertifiedOmmited.filter(e => e.isCertified).length;

        return (
            <Collapse onChange={this.onCollapseChange} className='dataset-properties' bordered={false} expandIcon={({isActive}) => <Icon type="caret-right" rotate={isActive ? -90 : 90} />} defaultActiveKey={properties.opened} activeKey={properties.opened}>
                <Panel header={t('Application')} key='application'>
                    <table>
                        <tbody>
                            {applications}
                        </tbody>
                    </table>
                    {/* <a onClick={this.props.handleBack} id='manage-applications'>{t('Manage Applications')}</a> */}
                </Panel>
                <Panel header={t('Type')} key='type'>
                    <table>
                        <tbody>
                            <tr>
                                <td><Checkbox onChange={() => this.toggleSubtypes('reports')} checked={reportsChecked} id="reports" /></td>
                                <td><label htmlFor="reports">{t('Report')}</label></td>
                                <td><Tooltip placement="right" title={t('Number of available reports')} mouseEnterDelay={1} overlayClassName='info-tooltip one-liner'>
                                    {reportsAmount}
                                </Tooltip></td>
                            </tr>
                            <tr><td><Checkbox onChange={() => this.toggleSubtypes('datasets')} checked={datasetsChecked} id="datasets" /></td>
                                <td><label htmlFor="datasets">{t('Dataset')}</label></td>
                                <td><Tooltip placement="right" title={t('Number of available datasets')} mouseEnterDelay={1} overlayClassName='info-tooltip one-liner'>
                                    {datasetsAmount}
                                </Tooltip></td></tr>
                        </tbody>
                    </table>
                </Panel>
                <Panel header={t('Certified status')} key='certified'>
                    <table>
                        <tbody>
                            <tr><td><Checkbox onChange={() => this.toggleCertified()} checked={properties.certified} id="certified" /></td>
                                <td><label htmlFor="certified">{t('Certified')}</label></td>
                                <td><Tooltip placement="right" title={t('Number of certified reports or datasets')} mouseEnterDelay={1} overlayClassName='info-tooltip one-liner'>
                                    {certifiedAmount}
                                </Tooltip></td></tr>
                        </tbody>
                    </table>
                </Panel>
                <Panel header={t('Owner')} key='owner'>
                    <Input.Search
                        placeholder={t('Search') + '...'}
                        onChange={this.onOwnerSearchChange}
                        className='project-chooser-search' />
                    <table>
                        <tbody>
                            <tr><td><Checkbox onChange={() => this.toggleAllOwners()} checked={allOwnersSelected} indeterminate={allOwnersIndeterminate} id="all-owners" /></td>
                                <td><label htmlFor="all-owners">{`(${t('All')})`}</label></td>
                                <td>({allOwnersAmount})</td></tr>
                            {owners}
                        </tbody>
                    </table>
                </Panel>
            </Collapse>
        );
    }
}

DatasetProperties.contextType = StateContext;
