import React, { Component } from 'react';
import { Form, Input, Popover, Icon, Tooltip } from 'antd';

import backendHelper from '../utilities/BackendHelper';
import StateContext from '../StateContext';
import MSTRIcon from './MSTRIcon';

const FormItem = Form.Item;

const formItemLayout = {
  colon: false,
  required: false,
};

const REGEX_ENV_NAME = /^\w+[\w\s]*$/;

const ENV_NAME_MAX_LENGHT = 150;

export default class EnvNameInput extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedSuggestion: {},
      isValidEnvUrl: false,
      curPos: 0,
    };
  }

    clearAllEnvSuggestions = () => {
      const { updateStateContext } = this.context;
      const suggestedEnvironments = { all: [], temp: [] };
      const recentProjects = [];
      const datasetProperties = [];
      updateStateContext({ suggestedEnvironments, recentProjects, datasetProperties });
      backendHelper.updateEnvSuggestions(suggestedEnvironments.all);
      backendHelper.updateRecentProjects(recentProjects);
      backendHelper.updateDatasetProperties(datasetProperties);
    }

    envNameFieldKey = (event) => {
      const { keyCode } = event;
      if (keyCode === 13) this.onEnvNameEnter();
      if (keyCode === 38) this.onArrowKey(-1); // up
      if (keyCode === 40) this.onArrowKey(+1); // down
    }

    fillFieldsWithSuggestion = (envName, url, loginMode, username) => {
      const { form } = this.props;
      const envNameField = document.getElementById('envName');
      const passField = document.getElementById('password');
      envNameField.blur();
      const { setFieldsValue } = form;
      setFieldsValue({ envName, url, loginMode, username });
      passField.focus();
    }

    onArrowKey = (incr) => {
      const { suggestedEnvironments: { temp } } = this.context;
      const { length } = temp;
      const { curPos } = this.state;
      const newPos = Math.min(Math.max(curPos + incr, 0), length);

      if (newPos !== curPos) {
        const selected = document.querySelector('.recent-environments ul li.selected');
        selected && selected.classList.remove('selected');

        let selectedSuggestion;
        if (newPos !== 0) {
          selectedSuggestion = temp[newPos - 1];
          const selector = `.recent-environments ul li:nth-child(${newPos})`;
          const row = document.querySelector(selector);
          row.classList.add('selected');
        }
        else if (newPos === 0) {
          selectedSuggestion = {};
        }
        this.setState({ selectedSuggestion, curPos: newPos });
      }
    }

    onEnvNameChange = (e) => {
      const { suggestedEnvironments: { all }, updateStateContext } = this.context;
      const envName = e.target.value;
      const isValidEnvUrl = REGEX_ENV_NAME.test(envName);
      this.setState({ isValidEnvUrl, selectedSuggestion: {} });
      let temp;
      if (envName) {
        temp = all.filter((env) => env.envName.toLowerCase().includes(envName.toLowerCase()));
      } else {
        temp = [...all];
      }
      updateStateContext({ suggestedEnvironments: { all, temp } });
    }


    onEnvNameEnter = () => {
      const { selectedSuggestion } = this.state;
      if (selectedSuggestion.envName) {
        const { envName, url, loginMode, username } = selectedSuggestion;
        this.fillFieldsWithSuggestion(envName, url, loginMode, username);
      }
      else {
        document.querySelector('#url').focus();
      }
    }

    removeSingleSuggestion = (envName) => {
      const { suggestedEnvironments, recentProjects, datasetProperties, updateStateContext } = this.context;
      const all = suggestedEnvironments.all.filter((e) => e.envName !== envName);
      const recentProjectsNew = recentProjects.filter((e) => e.envName !== envName);
      const datasetPropertiesNew = datasetProperties.filter((e) => e.envName !== envName);
      const suggestedEnvironmentsNew = { all, temp: all };

      updateStateContext({ suggestedEnvironments, recentProjects: recentProjectsNew, datasetProperties });
      backendHelper.updateEnvSuggestions(suggestedEnvironmentsNew.all);
      backendHelper.updateRecentProjects(recentProjectsNew);
      backendHelper.updateDatasetProperties(datasetPropertiesNew);

      const envNameField = document.getElementById('envName');
      envNameField.focus();
    }

    // eslint-disable-next-line react/destructuring-assignment
    generateSuggestionsList = () => this.context.suggestedEnvironments.temp.map((e) => {
      const { envName, url, loginMode, username } = e;
      return (
        <li key={envName}>
          <button onClick={() => this.fillFieldsWithSuggestion(envName, url, loginMode, username)} title={`${envName}\n\n${url}`} type="button" className="recent-env-button">
            <h3>{envName}</h3>
            <p>{url}</p>
          </button>
          <button type="button" onClick={() => this.removeSingleSuggestion(envName)} className="remove-env-button">
            <Icon type="close" />
          </button>
        </li>
      );
    })

    generateSuggestionPopoverContent = () => {
      const { t } = this.context;
      return (
        <>
          <ul className="recent-environment-list">
            {this.generateSuggestionsList()}
          </ul>
          <p className="clear-recent">
            <button onClick={() => this.clearAllEnvSuggestions()} type="button">{t('Clear all recent')}</button>
          </p>
        </>
      );
    }

    render() {
      const { form } = this.props;
      const { getFieldDecorator } = form;
      const { t, suggestedEnvironments } = this.context;
      const errorMsg = (
        <Tooltip placement="bottom" title={t('Environment name can contain only latin letters, numbers, spaces and underscores.')}>
          <div>
            <MSTRIcon type="red-info" />
            <span>{t('Enter a valid environment name')}</span>
          </div>
        </Tooltip>
      );
      const validateTrigger = 'onBlur';
      return (
        <FormItem label={`${t('Environment Name')}*`} {...formItemLayout} wrapperCol={{ md: { span: 8 } }}>
          <Popover
            content={this.generateSuggestionPopoverContent()}
            title={t('Recent environments')}
            trigger="focus"
            placement="bottomLeft"
            overlayClassName={suggestedEnvironments.temp.length > 0 ? 'recent-environments' : 'recent-environments--hidden'}
          >
            {getFieldDecorator('envName', {
              rules: [{
                required: true,
                message: errorMsg,
                pattern: REGEX_ENV_NAME,
              }],
              onChange: this.onEnvNameChange,
              validateTrigger,
            })(
              <Input placeholder={t('A nickname for your environment')} onKeyUp={this.envNameFieldKey} maxLength={ENV_NAME_MAX_LENGHT} autoComplete="off" />,
            )}
          </Popover>
        </FormItem>
      );
    }
}

EnvNameInput.contextType = StateContext;
