/* eslint-disable react/sort-comp */
import React, { Component } from 'react';
import { message, Modal, Spin, Icon } from 'antd';

import backendHelper, { backend } from '../utilities/BackendHelper';
import StateContext, { modes } from '../StateContext';

export default class BackendManager extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modalText: '',
    };
  }

  addEnvToSuggestions = (envInfo) => {
    const { updateStateContext } = this.context;
    const all = JSON.parse(envInfo);
    const temp = all;
    updateStateContext({ suggestedEnvironments: { all, temp } });
  }

  addRecentProjects = (recentProjectsStr) => {
    const { updateStateContext } = this.context;
    const recentProjects = JSON.parse(recentProjectsStr);
    updateStateContext({ recentProjects });
  }

  addDatasetProperties = (properties) => {
    const { updateStateContext } = this.context;
    const datasetProperties = JSON.parse(properties);
    updateStateContext({ datasetProperties });
  }

  displayErrorMessage(type) {
    const { t, updateStateContext } = this.context;
    let text;
    switch (type) {
      case 'RconnectToEnvError':
        text = t('Error when connecting to the environment. See RStudio console for details.');
        break;
      case 'RfetchError':
        text = t('Error when fetching the dataset. See RStudio console for details.');
        break;
      case 'RfileError':
        text = t('There was a problem saving a file with user\'s preferences.');
        break;
      case 'RfolderError':
        text = t('There was a problem creating the user\'s settings directory. See RStudio console for details.');
        break;
      case 'RexportError':
        text = t('Error when exporting the dataset. See RStudio console for details.');
        break;
      case 'RcubeNotPublishedError':
        text = t('Error when fetching dataset. The cube is not published.');
        break;
      case 'RemptyDatasetError':
        text = t('The dataset you were trying to fetch contains no data.');
        break;
      case 'RreportOverwriteError':
        text = t('Error when exporting the dataset. Cannot overwrite a report.');
        break;
      default:
        text = t('Error occured. See RStudio console for details.');
    }
    message.destroy();
    backendHelper.sendMessageToConsole(text);
    message.error(text, 6);
    document.querySelector('.left-panel').focus();
    updateStateContext({ importOrExportInProgress: false });
  }

  displayFetchStartMessage(name, type) {
    const { t, updateStateContext } = this.context;
    const template = t(`Fetching {{datasetName}} ${type}. Please wait.`, { datasetName: name });
    const msg = t(template, { datasetName: name });
    this.setState({ modalText: msg });
    updateStateContext({ importOrExportInProgress: true });
    backendHelper.sendMessageToConsole(msg);
  }

  displayFetchLoadingMessage(offset, total) {
    const { t } = this.context;
    const percent = `${((Number(offset) * 100) / (Number(total))).toFixed(0)}%`;
    const modalText = `${t('Rows fetched')}: ${offset}/${total} (${percent})`;
    this.setState({ modalText });
  }

  // eslint-disable-next-line no-unused-vars
  displayFetchSuccessMessage(datasetType, datasetName, time) {
    const { t, updateStateContext } = this.context;
    // const template = `${datasetType} \`{datasetName}\` fetched and parsed. It took {time} s.`;
    // const text = t(template, {datasetType, datasetName, time})
    const template = t(`${datasetType} {{datasetName}} imported successfully.`, { datasetName });
    const text = t(template, { datasetName });
    backendHelper.sendMessageToConsole(text);
    message.destroy();
    message.success(text, 3);
    updateStateContext({ mode: modes.BROWSING, reloadDataSource: false, importOrExportInProgress: false });
    document.querySelector('.right-panel').focus();
  }

    // eslint-disable-next-line no-unused-vars
    displayExportSuccessMessage = (datasetName, time) => {
      const { t, updateStateContext } = this.context;
      // const template = `Dataset successfully exported. It took {time} s.`;
      // const text = t(template, {time})
      const template = t('Dataset {{datasetName}} exported successfully.', { datasetName });
      const text = t(template, { datasetName });
      backendHelper.sendMessageToConsole(text);
      message.destroy();
      message.success(text, 3);
      updateStateContext({ importOrExportInProgress: false });
      document.querySelector('.left-panel').focus();
    }

    displayExportStartMessage = (name) => {
      const { t, updateStateContext } = this.context;
      const msg = t('Exporting {{datasetName}} dataset. Please wait.', { datasetName: name });
      updateStateContext({ importOrExportInProgress: true });
      this.setState({ modalText: msg });
    }

    updateDataFramesList = (dataFramesStr) => {
      const { updateStateContext } = this.context;
      const dataFrames = JSON.parse(dataFramesStr);
      updateStateContext({ dataFrames });
    }

    updateDataFrameContent = (dataframeContentObj, dataframeName, _fromJSON) => {
      const { updateStateContext } = this.context;
      const fromJSON = !!_fromJSON;
      const dataframeContent = {
        name: dataframeName,
        content: (fromJSON ? JSON.parse(dataframeContentObj) : dataframeContentObj),
      };
      updateStateContext({ dataframeContent });
    }

    setBackendEnvName = (backendEnvName) => {
      const { updateStateContext } = this.context;
      updateStateContext({ backendEnvName });
    }

    updateBackendParameters = (params) => {
      const { updateStateContext } = this.context;
      const backendParameters = JSON.parse(params);
      updateStateContext({ backendParameters });
    }

    automaticLogin = async (credentials, token, mode, selectedProjects) => {
      if (mode === modes.AUTHENTICATION) return;
      const { api, updateStateContext } = this.context;
      const { envName } = credentials;
      const { tokenChecker, keepSessionAlive } = this.props;
      api.setCredentials(credentials);
      api.tokenValue = token;
      const isSessionAlive = await api.isSessionAlive();
      let newMode;
      if (isSessionAlive) {
        await api.getSessionInfo();
        tokenChecker();
        keepSessionAlive();
        if (mode === modes.FILTER_SELECTION) {
          newMode = modes.BROWSING;
        }
        else if (mode === modes.EXPORT_DIR_SELECT || mode === modes.EXPORT_DATA_WRANGLER) {
          newMode = modes.EXPORT_DF_SELECT;
        }
        else if (!mode) {
          newMode = modes.AUTHENTICATION;
        }
        else {
          newMode = mode;
        }
      }
      else {
        newMode = modes.AUTHENTICATION;
      }
      updateStateContext({ mode: newMode, selectedProjects, envName });
    }

    toggleImportOrExportInProgress = (state) => {
      const { updateStateContext } = this.context;
      updateStateContext({ importOrExportInProgress: state });
    }

    showAuthenticationPage = () => {
      const { updateStateContext } = this.context;
      updateStateContext({ mode: modes.AUTHENTICATION });
    }

    updatePackageVersionNumber = (packageVersion) => {
      const { updateStateContext } = this.context;
      updateStateContext({ packageVersion });
    }

    render() {
      const { t, importOrExportInProgress } = this.context;
      const { modalText } = this.state;
      const antIcon = <Icon type="loading" style={{ fontSize: 24 }} spin />;
      const visible = importOrExportInProgress && backend === 'RStudio';
      return (
        <Modal visible={visible} closable={false} zIndex={20} footer={null}>
          <div className="loading-modal-container">
            <div className="loading-modal-spin">
              <Spin indicator={antIcon} />
            </div>
            <div className="loading-modal-text">
              <p>
                {modalText}
              </p>
              <p>
                {t('To cancel, press the Stop button in the Viewer tab.')}
              </p>
            </div>
          </div>
        </Modal>
      );
    }
}

BackendManager.contextType = StateContext;
