import React, { Component } from 'react';
import { Tree, Icon, Input, Tooltip, Button } from 'antd';
import { LoadingText } from 'mstr-react-library';

import '../style/ProjectChooser.css';

import backendHelper, { backend } from '../utilities/BackendHelper';
import Footer from './Footer';
import MainContainer from './MainContainer';
import StateContext, { modes } from '../StateContext';
import { compareByName, disableFocusOnMouseClick } from '../utilities/Utilities';
import UserTooltip from './UserTooltip';
import MSTRIcon from './MSTRIcon';

const { TreeNode } = Tree;
const icon = <MSTRIcon type="project-collapsed" />;

export default class ProjectChooser extends Component {
  constructor(props) {
    super(props);
    this.state = {
      allProjects: [],
      recentProjects: [],
      recentProjectsIds: [],
      selectedProjects: {
        all: [],
        recent: [],
      },
      loading: true,
      sortingType: -1,
      recentListExpanded: true,
    };
  }

    componentDidMount = async () => {
      const { envName, api, recentProjects, updateStateContext } = this.context;
      const envInfo = recentProjects.find((e) => e.envName === envName);
      const recentProjectsIds = envInfo ? envInfo.projectList : [];
      const all = recentProjectsIds;
      const recent = recentProjectsIds;
      const projects = await api.getProjects();
      const allProjects = projects.sort(compareByName);
      const recentProjectsNew = allProjects.filter((e) => recentProjectsIds.includes(e.id));
      this.setState({ allProjects, recentProjects: recentProjectsNew, recentProjectsIds, loading: false, selectedProjects: { all, recent }, projectsSaved: { all: allProjects, recent: recentProjectsNew } },
        () => { this.parseToRealCheckboxes(); });
      updateStateContext({ selectedProjects: recent });
      document.addEventListener('keydown', this.handleKeyDown);
      disableFocusOnMouseClick('.project-chooser-header button, .ant-tree-checkbox-inner, button');
    }

    componentWillUnmount() {
      document.removeEventListener('keydown', this.handleKeyDown, false);
    }

    handleKeyDown = (e) => {
      if (e.keyCode === 13) {
        const prevented = [...document.querySelectorAll('.user-tooltip button, .links a, .project-chooser-header button')];
        if (!prevented.includes(e.target)) {
          document.querySelector('#next').click();
        }
      }
    }

    parseToRealCheckboxes = () => {
      const checkboxes = document.querySelectorAll('.ant-tree-checkbox-inner');
      checkboxes.forEach((e) => {
        e.setAttribute('role', 'checkbox');
        e.setAttribute('tabindex', 0);
        e.addEventListener('keydown', this.handleKeyOnCheckbox.bind(e));
      });
    }

    handleCancel = () => {
      backendHelper.closeConnector();
    }

    handleNext = () => {
      const { recentProjects, updateStateContext } = this.context;
      backendHelper.updateRecentProjects(recentProjects);
      updateStateContext({ mode: modes.BROWSING });
    }

    onCheck = (treeName, keys, event) => {
      const { selectedProjects, recentProjectsIds } = this.state;
      const itemName = event.node.props.eventKey;
      const projectIds = itemName.indexOf('all') === 0 ? event.node.props.children.map((e) => e.key) : [itemName];
      const { checked } = event;
      let all = [...selectedProjects.all];
      let recent = [...selectedProjects.recent];

      if (checked) {
        projectIds.forEach((e) => {
          all.push(e);
          recentProjectsIds.includes(e) && recent.push(e);
        });
      }
      else {
        all = all.filter((e) => !projectIds.includes(e));
        recent = recent.filter((e) => !projectIds.includes(e));
      }
      this.updateProjectLists(recent, all);
    }

    onSearchChange = (e) => {
      const { projectsSaved } = this.state;
      const searchText = e.target.value.toLowerCase();
      const allProjects = projectsSaved.all.filter((element) => element.name.toLowerCase().includes(searchText));
      const recentProjects = projectsSaved.recent.filter((element) => element.name.toLowerCase().includes(searchText));
      this.setState({ allProjects, recentProjects });
    }

    removeRecentProject = (id) => {
      const { recentProjects, selectedProjects, projectsSaved } = this.state;
      const recentProjectsNew = recentProjects.filter((e) => e.id !== id);
      const recent = selectedProjects.recent.filter((e) => e !== id);
      const all = selectedProjects.all.filter((e) => e !== id);
      const projectsSavedNew = { all: projectsSaved.all, recent: recentProjects };
      this.setState({ recentProjects: recentProjectsNew, projectsSaved: projectsSavedNew });
      this.updateProjectLists(recent, all);
    }

    sortProjectList = (typeCode) => {
      const { allProjects, recentProjects } = this.state;
      const allProjectsNew = [...allProjects];
      const recentProjectsNew = [...recentProjects];
      const sorter = ((a, b) => ((a.name > b.name) ? typeCode : -typeCode));
      allProjectsNew.sort(sorter);
      recentProjectsNew.sort(sorter);
      const sortingType = -typeCode;
      this.setState({ allProjects: allProjectsNew, recentProjects: recentProjectsNew, sortingType });
    }

    // eslint-disable-next-line react/destructuring-assignment
    sortProjectIdsByProjectNames = (idList) => this.state.projectsSaved.all.map((e) => e.id).filter((e) => idList.includes(e))

    updateProjectLists = (recentUnsorted, allUnsorted) => {
      const { recentProjects, updateStateContext } = this.context;
      const recent = this.sortProjectIdsByProjectNames(recentUnsorted);
      const all = this.sortProjectIdsByProjectNames(allUnsorted);
      const { envName } = this.context;
      const curEnvObj = recentProjects.find((e) => e.envName === envName);
      const curEnv = curEnvObj || { envName, projectList: [] };
      curEnv.projectList = [...all];
      let recentEnvs = recentProjects.filter((e) => e.envName !== envName);
      recentEnvs = [...recentEnvs, curEnv];
      this.setState({ selectedProjects: { all, recent } });
      updateStateContext({ selectedProjects: all, recentProjects: recentEnvs });
    }

    onTreeExpand = (expandedKeys, { expanded, node }) => {
      if (node.props.eventKey === 'all-recent') {
        this.setState({ recentListExpanded: expanded });
      }
    }

    handleKeyOnCheckbox(event) {
      const { keyCode } = event;
      if (keyCode === 32) {
        this.click();
      }
    }

    renderTree = (projectList, name) => {
      const { t } = this.context;
      const { loading, recentListExpanded, selectedProjects } = this.state;
      const treeNodes = projectList.map((project) => {
        const displayedName = <Tooltip title={project.name} mouseEnterDelay={1} placement="right" overlayClassName="no-arrow">{project.name}</Tooltip>;
        return <TreeNode title={displayedName} key={project.id} dataRef={project} selectable={false} onLoad={this.onTreeNodeLoad} icon={icon} />;
      });

      const removeButtons = projectList.map((e) => {
        const title = t('Remove `{{projectName}}`', { projectName: e.name });
        return (
          <li key={e.id}>
            <Tooltip title={title} placement="left">
              <Icon type="close" onClick={() => this.removeRecentProject(e.id)} />
            </Tooltip>
          </li>
        );
      });

      if (loading) return <div className="center-wrapper application-list-loading"><LoadingText text={t('Application list is loading...')} /></div>;

      return (
        <div className="tree-container checkbox-container">
          <div className="tree-title">
            {t(name.charAt(0).toUpperCase() + name.slice(1))}
            <div className="tree-count">
              {projectList.length}
            </div>
          </div>
          <div className="tree-content">
            {name === 'recent' && recentListExpanded
                        && (
                        <ul className="remove-buttons">
                          {removeButtons}
                        </ul>
                        )}
            <Tree
              checkable
              defaultExpandAll
              checkedKeys={selectedProjects[name]}
              onCheck={(keys, event) => this.onCheck(name, keys, event)}
              showIcon
              onExpand={this.onTreeExpand}
            >
              <TreeNode title={`(${t('All')})`} key={`all-${name}`} icon={icon}>
                {treeNodes}
              </TreeNode>
            </Tree>
          </div>
        </div>
      );
    }

    render() {
      const { t } = this.context;
      const { sortingType, selectedProjects, loading, recentProjects, allProjects } = this.state;

      const label = sortingType === 1 ? t('Sort ascending') : t('Sort descending');
      const header = (
        <div className="project-chooser-header">
          <h2>{t('Select Applications')}</h2>
          <div>
            <Tooltip title={label} placement="bottom">
              <button onClick={() => this.sortProjectList(sortingType)} type="button" aria-label={label}><Icon type={sortingType === 1 ? 'sort-ascending' : 'sort-descending'} /></button>
            </Tooltip>
            <Input.Search
              placeholder={`${t('Search')}...`}
              onChange={this.onSearchChange}
              className="project-chooser-search"
            />
            {
                        backend === 'Jupyter' && <UserTooltip />
                    }
          </div>
        </div>
      );

      const okButtonDisabled = selectedProjects.all.length > 0;
      const okButtonTooltipText = !okButtonDisabled ? t('This button is currently disabled because you didn\'t select any applications') : null;
      return (
        <MainContainer>
          <div className="project-chooser main-content">
            {!loading && header}
            <div className="recent-projects">
              {recentProjects.length > 0 && this.renderTree(recentProjects, 'recent')}
            </div>
            <div className="all-projects">
              {this.renderTree(allProjects, 'all')}
            </div>
          </div>
          <Footer
            right={(
              <div>
                <Tooltip placement="topRight" title={okButtonTooltipText} mouseEnterDelay={1} overlayClassName="info-tooltip">
                  <Button id="next" type="primary" onClick={this.handleNext} disabled={!okButtonDisabled}>{t('OK')}</Button>
                </Tooltip>
              </div>
            )}
          />
        </MainContainer>
      );
    }
}

ProjectChooser.contextType = StateContext;
