import React, { Component } from 'react';
import { Row, Button, Table, Tooltip } from 'antd';
import TopBar from './TopBar';
import MainContainer from './MainContainer';
import Footer from './Footer';
import StateContext, { modes } from '../StateContext';
import backendHelper from '../utilities/BackendHelper';
import { sortGlobal } from './folder-browser/object-browser/object-browser';
import { addOrRemoveItemToArray } from '../utilities/Utilities';

import '../style/DataframeExport.scss';

const COLUMN_DEFINITIONS = [
  {
    title: 'Name',
    key: 'name',
    sorter: (a, b) => sortGlobal(a.name, b.name),
    render: ({ name }) => (
      name
    ),
    className: 'dataframe-name',
  },
  {
    title: 'Columns',
    key: 'columns',
    sorter: (a, b) => sortGlobal(a.columns, b.columns),
    render: ({ columns }) => (
      columns
    ),
    className: 'dataframe-columns',
  },
  {
    title: 'Rows',
    key: 'rows',
    sorter: (a, b) => sortGlobal(a.rows, b.rows),
    render: ({ rows }) => (
      rows
    ),
    className: 'dataframe-rows',
  },
];

export default class DataframeSelect extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedDirectory: {
        type: '',
        id: '',
      },
      searchText: '',
    };
  }

  componentDidMount() {
    backendHelper.onExportDataMode();
  }

  handleBack = () => {
    const { updateStateContext } = this.context;
    updateStateContext({ mode: modes.BROWSING });
  }

  handleNext = () => {
    const { updateStateContext } = this.context;
    updateStateContext({ mode: modes.EXPORT_DIR_SELECT });
  }

  handleWrangler = () => {
    const { updateStateContext } = this.context;
    updateStateContext({ mode: modes.EXPORT_DATA_WRANGLER });
  }

  onSearch = (e) => {
    this.setState({ searchText: e.target.value.toLowerCase() });
  }

  onTreeSelect = (selectedKeys, e) => {
    const { id, type } = e.node.props.dataRef;
    const typeName = type ? 'folder' : 'project';
    this.setState({ selectedDirectory: { type: typeName, id } });
  }

  selectRow = ({ name }) => {
    const { selectedDataframes, updateStateContext } = this.context;
    const selectedDataframesNew = addOrRemoveItemToArray(name, selectedDataframes);
    updateStateContext({ selectedDataframes: selectedDataframesNew });
  }

  selectAllRows = (selectedRows) => {
    const { updateStateContext } = this.context;
    const selectedDataframes = selectedRows.map((e) => e.name);
    updateStateContext({ selectedDataframes });
  }

  render() {
    const { searchText } = this.state;
    const { t, backendEnvName, selectedDataframes, dataFrames } = this.context;
    const dataSource = dataFrames.filter((e) => e.name.toLowerCase().startsWith(searchText));
    const rowSelection = {
      selectedRowKeys: selectedDataframes,
      type: 'checkbox', // TODO: to be removed after multichoice added
      onSelect: (record) => this.selectRow(record),
      onSelectAll: (selected, selectedRows) => this.selectAllRows(selectedRows),
    };
    const continueButtonDisabled = selectedDataframes.length < 1;

    const continueButtonTooltipTitle = continueButtonDisabled ? t('This button is currently disabled because you didn\'t select any dataframe') : null;
    return (
      <MainContainer>
        <Row className="full-height">
          <TopBar onSearch={this.onSearch} searchText={searchText} />
          <Row className="dataframe-selection table-container">
            <h2>{`Dataframes > ${backendEnvName}`}</h2>
            <Table
              dataSource={dataSource}
              rowSelection={rowSelection}
              rowKey={(item) => item.name}
              columns={COLUMN_DEFINITIONS.map((item) => ({ ...item, title: t(item.title) }))}
              pagination={false}
              onRow={(record) => ({
                onClick: () => {
                  this.selectRow(record);
                },
              })}
            />
          </Row>
        </Row>
        <Footer
          left={<Button id="back" onClick={this.handleBack}>{t('Back')}</Button>}
          right={(
            <div>
              <Tooltip placement="topRight" title={continueButtonTooltipTitle} mouseEnterDelay={1} overlayClassName={continueButtonDisabled ? 'info-tooltip' : 'hidden'}>
                <Button id="wrangler" onClick={this.handleWrangler} disabled={continueButtonDisabled}>{t('Wrangle Data')}</Button>
              </Tooltip>
              <Tooltip placement="topRight" title={continueButtonTooltipTitle} mouseEnterDelay={1} overlayClassName={continueButtonDisabled ? 'info-tooltip' : 'hidden'}>
                <Button id="next" onClick={this.handleNext} type="primary" disabled={continueButtonDisabled}>{t('Continue')}</Button>
              </Tooltip>
            </div>
)}
        />
      </MainContainer>
    );
  }
}

DataframeSelect.contextType = StateContext;
