import React, { Component } from 'react';
import { Input } from 'antd';

import '../style/TopBar.scss';
import StateContext, { modes } from '../StateContext';
import { disableFocusOnMouseClick } from '../utilities/Utilities';

export default class TopBar extends Component {
  componentDidMount() {
    disableFocusOnMouseClick('.top-nav li a');
  }

  changeMode = (mode) => {
    const { updateStateContext } = this.context;
    updateStateContext({ ...this.context, mode });
  }

  render() {
    const { t, mode } = this.context;
    const { onSearch, searchText } = this.props;

    return (
      <div className="top-bar">
        <div className="top-nav">
          <ul>
            <li><button onClick={() => this.changeMode(modes.BROWSING)} className={mode === modes.BROWSING ? 'active' : 'inactive'} id="import-data-tab-button" type="button">{t('Import Data')}</button></li>
            <li><button onClick={() => this.changeMode(modes.EXPORT_DF_SELECT)} className={mode.startsWith('export') ? 'active' : 'inactive'} id="export-data-tab-button" type="button">{t('Export Data')}</button></li>
          </ul>
        </div>
        <div className="top-bar__right">
          <div className="object-search">
            <Input.Search
              placeholder={`${t('Search')}...`}
              onChange={onSearch}
              className="project-chooser-search"
              value={searchText}
            />
          </div>
        </div>
      </div>
    );
  }
}

TopBar.contextType = StateContext;
